<!--异常处理tabble-->
<template>
    <div style="width: 100%" class="cus-table">
        <el-table v-table-scroll style="width: 100%" :tree-props="{children: 'subBills'}" row-key="index"
            :data="tableData" :header-cell-style="{background:'#E6EAFE',color:'#606266'}">
            <el-table-column label="序号" prop="index" width="90" fixed="left" />
            <el-table-column label="所属平台" prop="kpName" width="200" fixed="left"></el-table-column>
            <el-table-column label="用电性质" prop="workType" width="90"></el-table-column>
            <el-table-column label="账单月份" prop="arSnum" width="120">
                <template slot-scope="scope">
                    <span>{{ changeMonth(scope.row['month']) }}</span>
                </template>
            </el-table-column>
            <el-table-column label="起度" prop="arSnum" width="100">
                <template slot-scope="scope">
                    <span>{{ scope.row['type']=='3' && !scope.row['isChild'] ? '': scope.row['arSnum']}}</span>
                    <!--type为3 代表合表 更换计量点的情况 父账单没有起止度-->
                </template>
            </el-table-column>
            <el-table-column label="止度" prop="arEnum" width="100表底差值">
                <template slot-scope="scope">
                    <span>{{ scope.row['type']=='3' && !scope.row['isChild'] ? '': scope.row['arEnum']}}</span>
                    <!--type为3 代表合表 更换计量点的情况 父账单没有起止度-->
                </template>
            </el-table-column>
            <el-table-column label="倍率" prop="blRate" width="80">
                <template slot-scope="scope">
                    <span>{{ scope.row['type']=='3' && !scope.row['isChild']? '': scope.row['blRate']}}</span>
                    <!--type为3 代表合表 更换计量点的情况 父账单没有倍率-->
                </template>
            </el-table-column>
            <el-table-column label="电量（kW·h）" prop="useEq" width="120">
                <template slot-scope="scope">
                    <span>{{ scope.row['type']=='3' && !scope.row['isChild']? '': scope.row['useEq']}}</span>
                    <!--type为3 代表合表 更换计量点的情况 父账单没有倍率-->
                </template>
            </el-table-column>
            <el-table-column label="分摊损耗比例" prop="loseRate" width="120"></el-table-column>
            <el-table-column label="损耗电量" prop="loseEq" width="80"></el-table-column>
            <el-table-column label="补录电量" prop="extraEq" width="80"></el-table-column>
            <el-table-column label="总电量（kW·h）" prop="allEq" width="130"></el-table-column>
            <el-table-column label="所属公司" prop="kcName" show-overflow-tooltip width="200"></el-table-column>
            <el-table-column label="所属路线" prop="glName" width="200" show-overflow-tooltip></el-table-column>
            <el-table-column label="计量点名称" prop="jpName" show-overflow-tooltip width="250"></el-table-column>
            <el-table-column label="用能单位" prop="sdName" show-overflow-tooltip width="240"></el-table-column>
            <el-table-column label="结算起止码时间" prop="ts" width="300">
                <template slot-scope="scope">
                    <span v-if="scope.row['arSt']">
                        {{changeTime(scope.row['arSt'])}}-{{changeTime(scope.row['arEt'])}}
                    </span>
                </template>
            </el-table-column>
            <el-table-column label="日用电明细" align="left" fixed="right" class-name="cusTable" width="90">
                <template slot-scope="scope">
                    <div class="table-btns">
                        <span v-if="defined(scope.row.dayPaper)" class="cus-btn" @click="handClick('日用电明细',scope.row)"
                            type="text" size="small">{{'查看'}}</span>
                    </div>
                </template>
            </el-table-column>
            <el-table-column label="现场电量确认单" align="left" fixed="right" class-name="cusTable" width="120">
                <template slot-scope="scope">
                    <div class="table-btns">
                        <span v-if="defined(scope.row.sitePaper)" class="cus-btn"
                            @click="handClick('现场电量确认单',scope.row)" type="text" size="small">查看
                        </span>
                    </div>
                </template>
            </el-table-column>
            <el-table-column label="操作" align="center" fixed="right" class-name="cusTable" width="105">
                <template slot-scope="scope">
                    <div class="table-btns">
                        <template v-if="!scope.row['isChild']">
                            <!--运维人员 只能编辑 账单生成过来的账单-->
                            <template v-if="isYwRole">
                                <template v-if="scope.row['solveType']<=4">
                                    <span class="cus-btn line" @click="handClick('编辑',scope.row)" type="text"
                                        size="small">编辑</span>
                                    <span class="cus-btn" @click="handClick('流转',scope.row)" type="text"
                                        size="small">流转</span>
                                </template>
                                <div v-else class="center"></div>
                            </template>
                            <!--市场人员 只能编辑 价格异常过来的账单-->
                            <template v-if="isMarketRole">
                                <template v-if="scope.row['solveType']==6||scope.row['solveType']==7">
                                    <span class="cus-btn line" @click="handClick('编辑',scope.row)" type="text"
                                        size="small">编辑</span>
                                    <span class="cus-btn" @click="handClick('流转',scope.row)" type="text"
                                        size="small">流转</span>
                                </template>
                                <div v-else class="center"></div>
                            </template>
                        </template>
                        <div v-else-if="scope.row['type']==3" class="center"></div>
                    </div>
                </template>
            </el-table-column>
        </el-table>
        <div class="page pagination-box">
            <el-pagination @size-change="handleSizeChange" @current-change="handleCurrentChange" :current-page="pageNum"
                :page-sizes="[10, 20, 50, 100]" :page-size="pageSize" layout="prev, pager, next, jumper,total, sizes"
                :total="total">
            </el-pagination>
        </div>
    </div>
</template>
<script>
    import {
        parseTime
    } from "@/utils/index";
    import tool from "@/utils/tool";
    import Table from "@/components/ElTableCommon.vue";
    import {
        ROLENAME
    } from "@/utils/config.js";
    import {
        mapState
    } from 'vuex';
    export default {
        extends: Table,
        name: 'exceptionList',
        data() {
            return {}
        },
        computed: {
            ...mapState({ //角色权限
                isMarketRole: state => state.permission.roles.includes(ROLENAME.marketer),
                isYwRole: state => state.permission.roles.includes(ROLENAME.yw),
                state: state => state.permission.roles,
            }),
        },
        methods: {
            defined: tool.defined,
            handClick(btnName, row) {
                if (row.type == 3 && btnName == '编辑') {
                    this.$emit('btnClick', btnName, row.subBills[row.subBills.length - 1]);
                } else {
                    this.$emit('btnClick', btnName, row);
                }
            }
        }
    }
</script>
<style scoped lang="scss">
    .page {
        display: flex;
        justify-content: flex-end;
    }

    .cus-btn {
        color: #2E4AF7;
        font-size: 14px;
        padding: 0 2px;
        cursor: pointer;
        line-height: 22px;
        position: relative;
    }

    .table-btns {
        display: flex;
        justify-content: space-between;

        .center {
            width: 100%;
        }

        .line:after {
            content: "";
            position: absolute;
            display: block;
            right: -12px;
            top: 6px;
            bottom: 0;
            height: 12px;
            width: 1px;
            background: #E3E5EC;
        }
    }


    /deep/ .active-row {
        background-color: #F5F7FA;
    }
</style>