<template>
    <!--异常处理-->
    <el-dialog title="异常处理" custom-class="cusDialog" :visible.sync="show" width="4rem" :before-close="handleClose">
        <el-form ref="form" :model="form" :rules="rules" class="form">
            <!--账单生成处理-->
            <div v-if="type==1" class="li">
                <div class="item">
                    <el-form-item prop="reason">
                        <div class="label"><span class="important">*</span>异常原因：</div>
                        <el-input type="textarea" :rows="2" placeholder="请输入备注" v-model="form.reason">
                        </el-input>
                    </el-form-item>
                </div>
            </div>
            <div v-if="type==1" class="li">
                <div class="item">
                    <el-form-item>
                        <div class="label"><span class="important">*</span>解决方式：</div>
                        <el-select v-model="solveType" placeholder="请选择解决方式">
                            <el-option v-for="item in values[type]" :key="item.label" :label="item.label"
                                :value="item.value"></el-option>
                        </el-select>
                    </el-form-item>
                </div>
            </div>

            <!--市场客户确认异常-->
            <div v-if="type==2" class="li">
                <div class="item">
                    <el-form-item prop="name">
                        <div class="label"><span class="important">*</span>异常原因：</div>
                        <el-select v-model="solveType" placeholder="请选择异常原因">
                            <el-option v-for="item in values[type]" :key="item.label" :label="item.label"
                                :value="item.value"></el-option>
                        </el-select>
                    </el-form-item>
                </div>
            </div>
            <div v-if="type==2" class="li">
                <div class="item">
                    <el-form-item prop="reason">
                        <div class="label"><span class="important">*</span>备　　注：</div>
                        <el-input type="textarea" :rows="2" placeholder="请输入备注" v-model="form.reason">
                        </el-input>
                    </el-form-item>
                </div>
            </div>

        </el-form>
        <span slot="footer" class="dialog-footer">
            <el-button type="primary" :loading="loading" @click="submit">提 交</el-button>
            <el-button @click="handleClose">关 闭</el-button>
        </span>
    </el-dialog>
</template>
<script>
    import {
        Message
    } from 'element-ui';

    import {
        parseTime
    } from "@/utils/index";
    export default {
        props: {
            show: {
                type: Boolean,
                default: false,
            },
            detailData: {
                type: Object,
                default: () => {},
            },
            loading: Boolean,
            type: {
                type: Number, // 异常类型 1显示所有异常状态 2：显示单价不确定 和其它
                default: 1,
            }
        },
        data() {
            return {
                solveType: 1,
                values: {
                    1: [{
                        label: '修改起止码',
                        value: 1
                    }, {
                        label: '修改起止码并上传日明细',
                        value: 2
                    }, {
                        label: '上传模拟发票',
                        value: 3
                    }, {
                        label: '修改起止码并上传模拟发票',
                        value: 4
                    }, {
                        label: '跳过起止码连续验证规则',
                        value: 5
                    }],
                    2: [{
                        label: '单价不确定',
                        value: 6
                    }]
                },
                form: {
                    reason: '',
                },
                rules: {
                    reason: [{
                        required: true,
                        message: '备注不能为空',
                        trigger: 'blur'
                    }]
                }
            }
        },
        mounted() {
            if (this.type) {
                this.solveType = this.type == 1 ? 1 : 6;
            }
        },
        methods: {
            parseTime,
            handleClose() {
                this.$emit('errorHand')
            },
            submit() {
                this.$refs.form.validate((valid) => {
                    if (valid) {
                        this.$emit('errorHand', this.detailData, {
                            reason: this.form.reason ? this.form.reason.trim() : '',
                            solveType: this.solveType + ''
                        })
                        this.form.reason = ''; //初始化原因
                    }
                })

            }
        },
        watch: {
            type: {
                handler(val) {
                    if (!val) return;
                    this.solveType = this.type == 1 ? 1 : 6;
                }
            }
        }
    }
</script>

<style scoped lang="scss">
    .form {
        .li {
            display: flex;

            /deep/ .el-select {
                width: 250px;
            }

            /deep/ .el-textarea__inner {
                width: 250px;
            }

            .item {
                width: 100%;
                display: flex;
                line-height: 32px;
                height: 32;

                .important {
                    color: red;
                }

                .label {
                    font-family: "PFC";
                    min-width: 0.8rem;
                    margin-right: 2px;
                    color: #333;
                    text-align: right;
                }

                .box {
                    width: 100%;
                    border-radius: 4px;

                    padding: 0 10px;
                    box-sizing: border-box;
                }

                .rt {
                    flex: 1;
                }

                .btns {
                    width: 100%;
                    display: flex;
                    justify-content: flex-end;

                }
            }
        }
    }
</style>