<template>
    <!--选择结算单-->
    <el-dialog title="流转-结算单选择" custom-class="cusDialog" :visible.sync="show" width="9rem" :before-close="handleClose">
        <div class="content-title">请选择流转结算单：
            <span v-if="this.currentRow&&this.currentRow.contractName" class="selected-label">
                {{this.currentRow.contractName}} \ {{this.currentRow.contractPrice}}￥ \
                {{this.currentRow.wkTy}}</span>
        </div>
        <div class="item">
            <el-table v-table-scroll ref="searchTable" highlight-current-row row-key="index" max-height="300"
                :data="searchRecordTableData" :header-cell-style="{background:'#E6EAFE',color:'#606266'}"
                @current-change="currentRowHander" size="small">
                <el-table-column type="index" label="序号" width="70" />
                <el-table-column label="所属公司" prop="company" show-overflow-tooltip width="220"></el-table-column>
                <el-table-column label="合同名称" prop="contractName" show-overflow-tooltip width="280"></el-table-column>
                <el-table-column label="合同编号" prop="contractCode" show-overflow-tooltip width="250"></el-table-column>
                <el-table-column label="账单月份" prop="billDate" width="120">
                    <template slot-scope="scope">
                        <span>{{ changeMonth(scope.row['timeEnd']) }}</span>
                    </template>
                </el-table-column>
                <el-table-column label="用电性质" prop="wkTy" width="100" show-overflow-tooltip></el-table-column>
                <el-table-column label="合同电量（kW·h）" prop="allEq" width="150"></el-table-column>
                <el-table-column label="结算起止码时间" prop="ts" width="300">
                    <template slot-scope="scope">
                        <span>{{changeTime(scope.row['timeBegin'])}}-{{changeTime(scope.row['timeEnd'])}}</span>
                    </template>
                </el-table-column>
                <el-table-column label="合同单价（￥/kW·h）" prop="contractPrice" width="180"></el-table-column>
                <el-table-column label="总价-含税（元）" prop="totalPrice" width="180"></el-table-column>
            </el-table>
            <div v-if="!haveContract" class="warning">提示：该账单并未关联合同，请关联后再试。</div>
        </div>
        <span slot="footer" class="dialog-footer">
            <el-button type="primary" :disabled="!currentRow.contractName" :loading="loading" @click="submit">流 转
            </el-button>
            <el-button @click="handleClose">关 闭</el-button>
        </span>
    </el-dialog>
</template>
<script>
    import {
        parseTime,
        formatNum
    } from "@/utils/index";
    import tool from '@/utils/tool'
    import {
        getQueryFinalBill
    } from '@/api/archives';

    export default {
        props: {
            show: { //显隐
                type: Boolean,
                default: false,
            },
            detailData: { //账单数据
                type: Object,
                default: () => {},
            },
            loading: Boolean
        },
        data() {
            return {
                currentRow: {},
                searchRecordTableData: [],
                haveContract: true, //是否有合同
            }
        },
        mounted() {
            if (!this.detailData) return;
            this.getDataById(this.detailData.id);
        },
        methods: {
            //选择搜索记录
            currentRowHander(val) {
                this.currentRow = val;
            },
            parseTime,
            changeMonth(t) {
                return parseTime(new Date(t), '{y}年{m}月')
            },
            changeTime(t) {
                return parseTime(new Date(t), '{y}/{m}/{d} {h}:{i}')
            },
            getDataById(billId) {
                if (!billId) return;
                this.currentRow = {}; //置空
                this.searchRecordTableData = []; //置空
                this.haveContract = true;
                getQueryFinalBill({
                    billId
                }).then((res) => {
                    this.haveContract = !!res.data;
                    this.searchRecordTableData = res.data ? res.data : [];
                });
            },
            handleClose() {
                this.$emit('selectHand')
            },
            //异常处理提交表单
            submit() {
                this.$emit('selectHand', {
                    finalBillId: this.currentRow.id
                })
            },
        },
        watch: {
            detailData: {
                handler(val) {
                    if (!val) return;
                    this.getDataById(val.id);
                },
                deep: true,
                immediate: true,
            },
        }
    }
</script>

<style scoped lang="scss">
    .selected-label {
        font-size: 12px;
        color: green;
    }

    .warning {
        margin-top: 16px;
    }

    .item /deep/ .el-table tr {
        cursor: pointer;
    }
</style>