<!--流程节点 和操作日志-->
<template>
    <div>
        <div class="content-title">流程节点</div>
        <a-steps :current="current" size="small" class="step">
            <a-step title="账单生成" />
            <a-step title="用户确认" />
            <a-step title="市场确认" />
            <a-step title="闭环结束" />
        </a-steps>
        <div class="content-title">操作日志</div>
        <div class="log-content">
            <div class="head">
                <div class="log-label">当前节点停留时间：{{step_duration}}</div>
                <div class="log-label">总耗时：{{total_duration}}</div>
            </div>
            <ul class="log-ul">
                <li v-for="item in list" class="log-li">
                    <span class="lt">
                        <template v-if="item.details.includes('${sitePaper}')">
                            <span>{{item.details.replace('${sitePaper}','')}}</span>
                            <a @click="showFile">点击查看</a>
                        </template>
                        <template v-else>{{item.details}}</template>
                    </span>
                    <span class="rt">{{item.duration}}</span>
                </li>
            </ul>
        </div>
    </div>
</template>

<script>
    import Steps from 'ant-design-vue/lib/steps';
    import 'ant-design-vue/lib/steps/style';
    import {
        parseTime
    } from "@/utils/index";
    import {
        getBillFlow
    } from "@/api/archives";
    import {
        baseProfileUrl
    } from '@/api/baseUrl.js';
    export default {
        name: 'steps',
        components: {
            ASteps: Steps,
            AStep: Steps.Step,
        },
        props: ['id', 'sitePaperFile'],
        data() {
            return {
                list: [],
                current: 1,
                step_duration: '', //停留时间
                total_duration: '', //总耗时
            }
        },
        mounted() {
            this.id && this.getById(this.id);
        },
        methods: {
            showFile() { //预览图片
                let previewUrl = baseProfileUrl + this.sitePaperFile;
                if (!this.sitePaperFile) {
                    this.$message.warning('暂无图片')
                    return;
                }
                this.$viewerApi({
                    images: [previewUrl],
                })
            },
            getById(id) { // 根据账单id获取操作日志
                getBillFlow({
                    billId: id
                }).then((result) => {
                    const data = result.data;
                    if (data) {
                        this.list = data.flowLogs;
                        this.current = data.flow * 1;
                        this.step_duration = data.step_duration
                        this.total_duration = data.total_duration
                    }
                })
            }
        },
        watch: {
            id: {
                handler(val) {
                    if (!val) return;
                    this.getById(val);
                },
                immediate: true,
            },
        }
    }
</script>

<style scoped lang="scss">
    .step {
        margin-bottom: 40px;
    }

    .content-title {
        font-family: 'PFC';
        color: #333333;
        font-size: 14px;
        line-height: 25px;
        font-weight: 600;
    }

    .log-content {


        .head {
            display: flex;

            .log-label {
                color: #666666;
                font-size: 12px;
                line-height: 22px;
                margin-right: 60px;
            }
        }

        .log-ul {
            margin-top: 30px;
            list-style-type: none;
            color: #666666;
            font-size: 12px;
            max-height: 300px;
            overflow-y: auto;

            .log-li {
                .lt {
                    max-width: 500px;
                }

                list-style-type: none;
                line-height: 40px;
                border-bottom: 1px solid #E4E4E4;
                margin-bottom: 8px;
                display: flex;
                justify-content: space-between;

                .rt {
                    padding-right: 20px;
                }
            }
        }
    }
</style>