<template>
    <!--编辑数据-->
    <div>
        <el-dialog :title="type==1?'编辑名称':'编辑数据-国网计量点'" custom-class="cusDialog" :visible.sync="show" width="6rem"
            :before-close="handleClose">
            <div class="form">
                <div class="li" v-if="type==1">
                    <div class="item">
                        <div class="label">井名称：</div>
                        <div class='rt'>
                            <el-input v-model="otherName" placeholder="请输入名称">
                                <template slot="prepend">{{formData.kpName}}</template>
                            </el-input>
                        </div>
                    </div>
                </div>
                <template v-if="type==2">
                    <div class="li">
                        <div class="item">
                            <div class="label">平台名称：</div>
                            <div class='rt'>
                                <div class="box">{{formData.kpName}}</div>
                            </div>
                        </div>
                    </div>
                    <div class="li">
                        <div class="item">
                            <div class="label">用能单位：</div>
                            <div class='rt'>
                                <div class="box">{{formData.sdName}}</div>
                            </div>
                        </div>
                    </div>
                    <div class="li">
                        <div class="item">
                            <div class="label">用电性质：</div>
                            <div class='rt'>
                                <div class="box">{{formData.workType}}</div>
                            </div>
                        </div>
                    </div>
                    <div class="li">
                        <div class="item">
                            <div class="label">结算月份：</div>
                            <div class='rt'>
                                <div class="box">{{changeMonth(formData.month)}}</div>
                            </div>
                        </div>
                    </div>

                    <div v-if="showStAndEtInput" class="li">
                        <div class="item">
                            <div class="label"><span class="important">*</span>起度：</div>
                            <div class='rt'>
                                <el-input v-model="formData.arSnum" type="number" min="0" placeholder="请输入电量">
                                </el-input>
                            </div>
                        </div>
                    </div>
                    <div v-if="showStAndEtInput" class="li">
                        <div class="item">
                            <div class="label"><span class="important">*</span>止度：</div>
                            <div class='rt'>
                                <el-input v-model="formData.arEnum" type="number" min="0" placeholder="请输入电量">

                                </el-input>
                            </div>
                        </div>
                    </div>
                    <div v-if="showStAndEtInput" class="li">
                        <div class="item">
                            <div class="label"><span class="important">*</span>损耗分摊：</div>
                            <div class='rt'>
                                <el-input v-model="formData.loseEq" type="number" min="0" placeholder="请输入电量">
                                    <template slot="append">kW·h</template>
                                </el-input>
                            </div>
                        </div>
                    </div>
                    <div v-if="showStAndEtInput" class="li">
                        <div class="item">
                            <div class="label">倍率：</div>
                            <div class='rt'>
                                <div class="box">{{formData.blRate}}</div>
                            </div>
                        </div>
                    </div>

                    <div v-if="showStAndEtInput" class="li">
                        <div class="item">
                            <div class="label">总电量：</div>
                            <div class='rt'>
                                <div class="box">{{calcAllEq}}</div>
                            </div>
                        </div>
                    </div>
                    <div v-else class="li">
                        <div class="item">
                            <div class="label"><span class="important">*</span>电量：</div>
                            <div class='rt'>
                                <el-input v-model="formData.allEq" type="number" min="0" placeholder="请输入电量">
                                    <template slot="append">kW·h</template>
                                </el-input>
                            </div>
                        </div>
                    </div>

                    <div class="li" v-if="this.isYes(this.formData.stateGirdPaperImp)">
                        <div class="item">
                            <div class="label"><span class="important">*</span>模拟发票：</div>
                            <div class='rt'>
                                <el-upload :class="{hide:files.length}" :action="fileUploadUrl" :headers="headers"
                                    :on-success="handleChange" :before-upload="beforeUploadFP" list-type="picture-card"
                                    :limit="2" :file-list="files">
                                    <i slot="default" class="el-icon-plus"></i>
                                    <div slot="file" slot-scope="{file}">
                                        <img v-if="isImg(file)" class="el-upload-list__item-thumbnail" :src="file.url"
                                            alt="">
                                        <div v-else class="box">
                                            <svg-icon :icon-class="getPreType(file)" class-name="logo-img">
                                            </svg-icon>
                                        </div>
                                        <span class="el-upload-list__item-actions">
                                            <span v-if="isImg(file)" class="el-upload-list__item-preview"
                                                @click="handlePictureCardPreview(file)">
                                                <i class="el-icon-zoom-in"></i>
                                            </span>
                                            <span class="el-upload-list__item-delete" @click="handleRemove(files,file)">
                                                <i class="el-icon-delete"></i>
                                            </span>
                                        </span>
                                    </div>
                                </el-upload>
                                <UploadProgress ref="uploadPreogress" />
                            </div>
                        </div>
                    </div>
                    <div class="li" v-if="this.isYes(this.formData.dayPaperImp)">
                        <div class="item">
                            <div class="label"><span class="important">*</span>日用电明细表：</div>
                            <div class='rt'>
                                <el-upload class="upload-demo" :action="fileUploadUrlGw" :headers="headers"
                                    :on-success="handleChangeDetail" :before-upload="beforeUpload" :on-error="error"
                                    :data="params" :show-file-list="false">
                                    <span class="fileBtn">{{fileList.length?fileList[0].name:'点击上传'}}</span>
                                </el-upload>
                            </div>
                        </div>
                    </div>
                </template>
            </div>
            <span slot="footer" class="dialog-footer">
                <el-button type="primary" :loading="loading" @click="submit">提 交</el-button>
                <el-button @click="handleClose">关 闭</el-button>
            </span>
        </el-dialog>
    </div>
</template>
<script>
    import {
        parseTime,
        formatNum
    } from "@/utils/index";
    import {
        fileUploadUrl,
        postStateGridFile,
        resourceUrl,
        billExport
    } from '@/api/archives';
    import {
        baseUrl,
        baseProfileUrl
    } from '@/api/baseUrl.js';
    import {
        getToken
    } from '@/utils/auth'
    import tool from '@/utils/tool'

    import UploadProgress from "@/components/UploadProgress.vue"; //进度条
    export default {
        components: {
            UploadProgress,
        },
        props: {
            show: {
                type: Boolean,
                default: false,
            },
            type: { //编辑类型 1编辑名称 2编辑数据(国网)
                type: Number,
                default: 1,
            },
            detailData: {
                type: Object,
                default: () => {},
            },
            loading: Boolean
        },
        data() {
            return {
                showStAndEtInput: false,
                formData: {
                    kwell: '1', //井名称
                    kpName: '', //平台名称
                    type: '', //自由国网类型
                    //国网数据维护字段
                    workType: '', //工作性质
                    sdName: '', //用能单位
                    month: '', // 结算月份
                    allEq: '', // 电量
                    loseEq: 0,
                    arSnum: 0,
                    arEnum: 0,
                    uploadEq: '', // 上传的日用明细表的电量
                },
                fileUploadUrl, //上传文件地址,日用明细
                files: [], //上传发票文件
                fileList: [], //上传日用明细表
                headers: {
                    uapAccessToken: getToken('accessToken'),
                    uapRefreshToken: getToken('refreshToken'),
                },
                fileUploadUrlGw: '', //上传日用明细地址
                params: { //上传文件的请求参数
                    id: ''
                },
                excelName: '', //excel名称
                upLoading: false,
                otherName: '',
            }
        },
        mounted() {
            this.detailData && this.init(this.detailData);
        },
        computed: {
            //总电量=（起度-止度）*倍率 + 损耗电量
            calcAllEq() {
                const sum = ((this.formData.arEnum - this.formData.arSnum).toFixed(6) * this.formData.blRate)
                    .toFixed(2);
                let calc = formatNum(sum) < 0 ? 0 : formatNum(sum);
                return formatNum(calc + Number(this.formData.loseEq))
            }
        },
        methods: {
            isYes: tool.isYes,
            init(val) { //初始化数据
                this.formData.id = val.id;
                this.formData.kwell = val.kwell;
                this.formData.kpName = val.kpName;
                this.formData.workType = val.workType;
                this.formData.sdName = val.sdName;
                this.formData.month = val.month;
                this.formData.allEq = val.allEq;
                this.formData.type = val.type;

                this.formData.arSnum = val.arSnum ? val.arSnum : 0;
                this.formData.arEnum = val.arEnum ? val.arEnum : 0;
                this.formData.blRate = val.blRate ? val.blRate : 0;
                this.formData.loseEq = val.loseEq ? val.loseEq : 0;

                this.formData.stateGridPaper = val.stateGridPaper;
                //测试
                // this.formData.dayPaperImp = 'Y'; //导入日用电明细
                // this.formData.stateGirdPaperImp = 'N'; //导入国网模拟发票
                // val.kcCode = 'KC0001'

                this.formData.dayPaperImp = val.dayPaperImp; //导入日用电明细
                this.formData.stateGirdPaperImp = val.stateGirdPaperImp; //导入国网模拟发票

                this.otherName = val.kwell ? val.kwell.replace(val.kpName, '') : '';
                this.params.id = val.id;
                this.fileUploadUrlGw = billExport(this.formData.id);

                //临时处理 国网计量中 除四川页岩气公司需要导入日明细表，其余只需输入起止码 损耗分摊
                this.showStAndEtInput = tool.showStAndEtFunc(val.kcCode)

                //日用明细表显示
                if (val.allEq && this.isYes(this.formData.dayPaperImp)) {
                    if (val.dayPaper) {
                        this.fileList = [{
                            url: '',
                            name: val.dayPaper,
                        }]
                    } else {
                        this.fileList = []
                    }
                    this.formData.uploadEq = val.allEq;
                } else if (val.allEq) { //发票上传
                    if (val.stateGridPaper) {
                        this.files = [{
                            url: this.getShowUrl(val.stateGridPaper),
                            name: this._getFileNameByUrl(val.stateGridPaper),
                        }];
                    } else {
                        this.files = [];
                    }
                    this.formData.uploadEq = val.allEq;
                } else {
                    this.files = [];
                    this.fileList = [];
                    this.formData.uploadEq = 0;
                }
                this.$refs.uploadPreogress && this.$refs.uploadPreogress.init();
            },
            //根据文件名获取文件名称
            _getFileNameByUrl(url) {
                if (!url) return '';
                const _index = url.indexOf('_');
                return _index !== -1 ? url.slice(_index + 1) : url;
            },
            handleClose() {
                this.$emit('editHand');
            },
            //下载文件
            downloadExcel(item) {
                const url = item.url;
                const fileName = item.name;
                const a = document.createElement('a');
                a.setAttribute('download', fileName);
                a.setAttribute('href', resourceUrl + "?resource=" + url.replace(baseProfileUrl, ''));
                a.click();
            },
            submit() {
                let opt = {};
                if (this.type == 1) { //编辑名称
                    opt = {
                        kwell: this.formData.kpName + this.otherName,
                        id: this.formData.id
                    }
                    this.$emit('editHand', opt, this.type);
                    return;
                }
                //国网编辑数据
                //需要模拟发票
                if (this.isYes(this.formData.stateGirdPaperImp)) {
                    if (!this.files[0]) {
                        if (!this.$refs.uploadPreogress.isLoading()) { //还没上传模拟发票
                            this.$message.warning('请上传模拟发票');
                        } else { //上传到一半
                            this.$message.warning('模拟发票正在上传，请稍后再试');
                        }
                        return;
                    }
                    opt.filePath = this._getUrl(this.files[0]);
                }
                // 需要日明细
                if (this.isYes(this.formData.dayPaperImp)) {
                    if (this.formData.allEq <= 0) {
                        this.$message.warning('请输入电量');
                        return;
                    }
                    if (!this.fileList.length) {
                        this.$message.warning('请上传日用明细表');
                        return;
                    }
                    if (this.formData.allEq != this.formData.uploadEq) {
                        this.$message.warning('日用明细表电量必须和输入的电量相等，请核查');
                        return;
                    }
                }

                if (this.showStAndEtInput) { //需要手动填写起止码
                    if (this.calcAllEq <= 0) {
                        this.$message.warning('总电量有误，请检查起止码或损耗分摊电量。');
                        return;
                    }
                    opt = {
                        ...opt,
                        arSnum: this.formData.arSnum,
                        arEnum: this.formData.arEnum,
                        loseEq: this.formData.loseEq,
                        useEq: formatNum(this.calcAllEq - this.formData.loseEq),
                        allEq: this.calcAllEq,
                        id: this.formData.id
                    }
                } else { //无需手动填写止码的账单 只检查总电量
                    if (this.formData.allEq <= 0) {
                        this.$message.warning('请输入电量');
                        return;
                    }
                    opt = {
                        ...opt,
                        allEq: this.formData.allEq,
                        id: this.formData.id
                    }
                }
                this.$emit('editHand', opt, this.type);
            },
            submitold() {
                let opt = '';
                if (this.type == 1) { //编辑名称
                    opt = {
                        kwell: this.formData.kpName + this.otherName,
                        id: this.formData.id
                    }
                } else if (this.isYes(this.formData.stateGirdPaperImp)) { //上传模拟发票
                    if (this.showStAndEtInput) { //需要起止码
                        if (this.calcAllEq <= 0) {
                            this.$message.warning('总电量有误，请检查起止码或损耗分摊电量。');
                            return;
                        }
                    } else { //无起止码的账单 只检查总电量
                        if (this.formData.allEq <= 0) {
                            this.$message.warning('请输入电量');
                            return;
                        }
                    }

                    if (!this.files[0]) {
                        if (!this.$refs.uploadPreogress.isLoading()) { //还没上传模拟发票
                            this.$message.warning('请上传模拟发票');
                        } else { //上传到一半
                            this.$message.warning('模拟发票正在上传，请稍后再试');
                        }
                        return;
                    }
                    if (this.showStAndEtInput) { //需要起止码的国网 如长宁
                        opt = {
                            arSnum: this.formData.arSnum,
                            arEnum: this.formData.arEnum,
                            loseEq: this.formData.loseEq,
                            useEq: formatNum(this.calcAllEq - this.formData.loseEq),
                            allEq: this.calcAllEq,
                            filePath: this._getUrl(this.files[0]),
                            id: this.formData.id
                        }
                    } else {
                        opt = {
                            allEq: this.formData.allEq,
                            filePath: this._getUrl(this.files[0]),
                            id: this.formData.id
                        }
                    }
                } else if (this.formData.dayPaperImp) { //没有发票 但有日用明细
                    if (this.formData.allEq <= 0) {
                        this.$message.warning('请输入电量');
                        return;
                    }
                    if (!this.fileList.length) {
                        this.$message.warning('请上传日用明细表');
                        return;
                    }
                    if (this.formData.allEq != this.formData.uploadEq) {
                        this.$message.warning('日用明细表电量必须和输入的电量相等，请核查');
                        return;
                    }
                    opt = {
                        allEq: this.formData.allEq,
                        filePath: this.excelName,
                        id: this.formData.id
                    }
                } else { //没有发票 也没有日明细

                }
                this.$emit('editHand', opt, this.type);
            },
            changeMonth(t) {
                return parseTime(new Date(t), '{y}年{m}月')
            },
            //获取完整绝对文件地址
            getShowUrl(url) {
                if (!url) return '';
                return baseProfileUrl + url;
            },
            //获取svg文件名
            getPreType(file) {
                const name = file.name.toLowerCase();
                const baseImg = ''
                if (name.indexOf('.doc') !== -1 || name.indexOf('.docx') !== -1) { // word
                    return 'w'
                } else if (name.indexOf('.pdf') !== -1) {
                    return 'pdf'
                } else if (name.indexOf('.ppt') !== -1 || name.indexOf('.pptx') !== -1) {
                    return 'ppt'
                } else if (name.indexOf('.xls') !== -1 || name.indexOf('.xlsx') !== -1) {
                    return 's'
                }
                return 'zip';
            },
            isImg(file) {
                const name = file.name.toLowerCase();
                return ['.png', '.jpeg', '.jpg'].find(item => {
                    return name.indexOf(item) !== -1;
                })
            },
            //获取保存的url地址
            _getUrl(item) {
                if (!item) return '';
                if (item.response) {
                    return item.response.fileName;
                } else if (item.url) {
                    return item.url.replace(baseProfileUrl, '');
                }
                return '';
            },
            //预览图片
            handlePictureCardPreview(file) {
                this.$viewerApi({
                    images: [file.url]
                })
            },
            //删除图片
            handleRemove(data, file) {
                let index = 0;
                data.map((item, index) => {
                    if (item.name === file.name) {
                        index = index;
                    }
                })
                data.splice(index, 1)
            },
            //模拟发票
            handleChange(res, file, fileList) {
                if (res.code == 200) {
                    this.files = fileList.slice(0);
                    this.$refs.uploadPreogress.success();
                } else {
                    this.msg(res.msg);
                    this.files = [];
                }
            },
            // 日用明细表上传回调
            handleChangeDetail(res, file, fileList) {
                this.upLoading = false;
                if (res.code == 200) {
                    this.fileList = fileList;
                    this.formData.uploadEq = res.data.useEq;
                    this.$message.success('上传成功');
                } else {
                    this.msg(res.msg);
                    this.fileList = [];
                }
                if (this.fileList.length >= 2) {
                    this.fileList = [this.fileList[1]];
                }
            },
            //日用明细表上传检查
            beforeUpload(file) {
                const name = file.name.toLowerCase();
                const isExcel = ['.xls', '.xlsx'].find(item => {
                    return name.indexOf(item) !== -1;
                })
                const isLt2M = file.size / 1024 / 1024 < 2;
                if (!isExcel) {
                    this.$message.error('请上传excel文件!');

                    return false;
                }
                if (!isLt2M) {
                    this.$message.error('上传文件大小不能超过 2MB!');
                    return false;
                }
                this.upLoading = true;
                this.excelName = name; //保存上传的excel名称
                return true;
            },
            //模拟发票格式验证
            beforeUploadFP(file) {
                if (this.$refs.uploadPreogress.isLoading()) return false;
                const name = file.name.toLowerCase();
                const isPngOrPdf = ['.png', '.jpeg', '.jpg', '.pdf'].find(item => {
                    return name.indexOf(item) !== -1;
                })
                const isLt2M = (file.size / 1024 / 1024) < 2;
                if (!isPngOrPdf) {
                    this.$message.error('请上传png、jpeg、jpg或pdf文件!');
                    return false;
                }

                this.$refs.uploadPreogress.start(); //开始增加进度

                //如果时图片且超过2M 压缩图片
                if (!isLt2M && !~name.indexOf('.pdf')) {
                    return tool.compressImg(file);
                } else {
                    return true;
                }
            },
            error() {
                this.upLoading = false;
            },
            msg(err) {
                this.$message.error('上传出错:' + err);
            },
        },
        watch: {
            detailData(val) {
                if (!val) return;
                this.init(val);
            },
            deep: true,
            immediate: true,
        }
    }
</script>

<style scoped lang="scss">
    .form {
        .li {
            display: flex;
            margin-bottom: 22px;



            .item {
                width: 100%;
                display: flex;
                line-height: 32px;
                height: 32;

                .label {
                    font-family: "PFC";
                    min-width: 0.8rem;
                    margin-right: 2px;
                    color: #333;
                    text-align: right;
                }

                .logo-img {
                    width: 95px;
                    height: 95px;
                    line-height: 0;
                    padding: 30px;
                    box-sizing: border-box;
                }

                .box {

                    width: 100%;
                    border-radius: 4px;
                    color: #666;

                    box-sizing: border-box;
                }

                .rt {
                    flex: 1;
                    padding: 0 10px;
                }

                .btns {
                    width: 100%;
                    display: flex;
                    justify-content: flex-end;

                }

                .fileBtn {
                    font-size: 14px;
                    color: #2E4AF7;
                    text-decoration: underline;
                }
            }
        }

        /deep/ .hide .el-upload--picture-card {
            display: none;
        }
    }
</style>