<!--市场客户确认-->
<template>
    <div>
        <SearchInputs @downHand="downHand" @searchHand="searchHand"></SearchInputs>
        <CusTable :tableData="tableData" @btnClick="btnClick" @handleSizeChange="handleSizeChange"
            @handleCurrentChange="handleCurrentChange" :pageNum="page.pageNum" :pageSize="page.pageSize" :total="total">
        </CusTable>

        <!--异常处理-->
        <ErrorDlModel :show="this.errorModelVisible" :detailData="detailData" :loading="loading" @errorHand="errorHand"
            :type="2">
        </ErrorDlModel>

        <!--弹框 现场电量确认单-->
        <el-dialog title="现场电量确认单" custom-class="cusDialog" :visible.sync="showTableDialog" width="10rem"
            :before-close="handleClose" @closed="closed">
            <!--弹框  流程-->
            <el-dialog width="6.8rem" title="流程跟踪表（当前节点）" :visible.sync="showTableFlowDialog" append-to-body>
                <BillFlow :id="billId" :sitePaperFile="detailData.sitePaperFile"></BillFlow>
                <span slot="footer" class="dialog-footer">
                    <el-button @click="closeFlowDialog">关 闭</el-button>
                </span>
            </el-dialog>
            <!--弹框  流程-->

            <!--打印-->
            <div class="print-body">
                <TempalteBill v-if="showPrintWd" :type="1" :tableData="detailData"></TempalteBill>
            </div>
            <span slot="footer" class="dialog-footer">
                <el-button v-show="haveFp" @click="showFp">模拟发票</el-button>
                <el-button type="primary" @click="html2Excel">下 载</el-button>
                <el-button type="primary" v-print="printObj">打 印</el-button>
                <el-button @click="showBillFlow">查看流程</el-button>
                <el-button @click="handleClose">取 消</el-button>
            </span>
        </el-dialog>
    </div>
</template>
<script>
    //组件
    import TableCommon from "@/components/TableCommon.vue";
    import CusTable from "./components/marketComfirm/BillTable.vue";
    import SearchInputs from "./components/billCreate/SearchInputsCreate.vue";
    import ErrorDlModel from "./components/common/ErrorDlModel.vue";
    import BillFlow from "./components/common/BillFlow.vue";
    import TempalteBill from "@/components/tempalteBill.vue";
    import {
        parseTime
    } from "@/utils/index";
    //接口
    import {
        getMarketerConfirm, //账单生成列表
        postBillWithdraw, //账单撤回
        postBillException, //异常处理提交
        exportBill,
    } from "@/api/archives.js";
    import {
        baseProfileUrl
    } from '@/api/baseUrl.js';
    //公用函数
    import tool from "@/utils/tool";
    import Table2Excel from 'table2excel.js'
    import {
        mapState
    } from 'vuex';

    export default {
        extends: TableCommon,
        name: 'MarketComfirm',
        components: {
            CusTable,
            SearchInputs,
            ErrorDlModel, //错误异常处理
            BillFlow, //流程
            TempalteBill, //打印
        },
        data() {
            return {
                errorModelVisible: false, //异常处理弹框

                showTableDialog: false, //现场电量确认单查看弹框
                showTableFlowDialog: false, //流程弹框
                detailData: {}, //单条账单详细数据

                billId: '', //账单id
                showPrintWd: false, //控制打印组件销毁和显示
                tab: 1,

                printObj: { //打印参数
                    id: "printArea",
                    popTitle: '　',
                },
                haveFp: false, //是否显示发票预览
            }
        },
        computed: {
            ...mapState({
                userId: state => state.user.userId, //当前登录的user
            })
        },
        mounted() {},
        methods: {
            isYes: tool.isYes,
            parseTime,
            _getData(params = {}) { // 通过接口 获取最新数据 如果无接口 则是第一次加载
                params = {
                    ...this.search,
                    ...this.page,
                }
                getMarketerConfirm(params).then(res => {
                    const data = res.rows ? res.rows : [];
                    //处理数据序号
                    data.forEach((item, index) => {
                        item.index = (this.page.pageNum - 1) * this.page.pageSize + index + 1;
                        if (item.type == 4) { //国网账单 加特殊标识
                            item.index += ' 【国】';
                        }
                        if (item.subBills && item.subBills.length) {
                            item.subBills.forEach((p, k) => {
                                p.index = item.index + '.' + (k + 1);
                                p.isChild = true;
                            })
                        }
                    });
                    this.tableData = data;
                    this.total = res.total;
                })
            },
            titleTab(tab) {
                this.tab = tab;
            },
            //下载excel
            html2Excel() {
                const name = this.detailData.kwell ? this.detailData.kwell : this.detailData.kpName;
                new Table2Excel('#printArea table').export(`${name}-${this.detailData.month}-现场电量确认单`);
            },
            showFp() { //发票预览
                let previewUrl = baseProfileUrl + this.detailData.stateGridPaper;
                if (!this.detailData.stateGridPaper) {
                    this.$message.warning('暂无图片')
                    return;
                }
                this.$viewerApi({
                    images: [previewUrl],
                })
            },
            //导出按钮
            downHand() {
                exportBill({
                    billState: '4',
                    ...this.search,
                }).then(tool.down)
            },
            //查看账单流程
            showBillFlow() {
                this.billId = this.detailData.id;
                this.showTableFlowDialog = true;
            },
            handleClose() {
                this.showTableDialog = false;
            },
            closeFlowDialog() {
                this.showTableFlowDialog = false;
            },
            //异常处理
            errorHand(row, data) {
                if (!row) {
                    this.errorModelVisible = false;
                    return;
                }
                this.loading = true;
                postBillException({
                    billId: row.id,
                    userId: this.userId,
                    solveType: data.solveType,
                    reason: data.reason,
                    date: this.parseTime(new Date()),
                }).then(res => {
                    this.loading = false; //停止加载
                    this.errorModelVisible = false; //关闭弹窗
                    this.resPublick(res);
                })
            },
            btnClick(name, row) {
                this.detailData = row;
                this.haveFp = this.isYes(row.stateGirdPaperImp);
                if (name == "查看") {
                    this.showPrintWd = true;
                    this.showTableDialog = true;
                } else if (name == "异常处理") {
                    this.errorModelVisible = true;
                }
            },

            //打印
            closed() {
                this.showPrintWd = false;
            },
            //统一的接口返回提示接口
            resPublick(res) {
                this.loading = false; //停止加载
                this.$message({
                    type: 'success',
                    message: '操作成功'
                })
                this.reloadTable();
            },
        }
    }
</script>

<style scoped lang="scss">
    .cus-title {
        display: flex;

        .title-btn {
            &.active {
                color: #2E4AF7;
            }

            cursor: pointer;
            margin-right: 60px;
            font-weight: 500;
            color: #333333;
            line-height: 20px;
            font-family: "PFC"
        }
    }
</style>