<template>
    <div class="search" @keyup.enter="search">
        <form-create v-model="formObj" :rule="rule" :option="options" @btn-search="search" @btn-reset="reset"
            @kp-code-select="handleSelectKp">
        </form-create>
        <div class="li">
            <el-button type="primary" @click="down">导 出</el-button>
        </div>
    </div>
</template>
<script>
    //接口
    import {
        getGlList,
        getKpList,
        getKcList,
        getPointsList
    } from "@/api/archives.js";
    import {
        parseTime,
        getPrevMonth
    } from "@/utils/index";
    import form from "../billCreate/SearchInputsCreate.vue";
    export default {
        extends: form,
        data() {
            return {
                //表单生成规则
                rule: [{
                    type: 'select',
                    field: "kcCode",
                    title: '公司名称：',
                    value: '',
                    options: [],
                    props: {
                        filterable: true,
                    },
                    on: {
                        change: (item) => {
                            //清空平台 计量点 线路
                            const formData = this.formObj.bind();
                            formData.glCode = '';
                            if (this._findRule('kpCode')) this.formObj.exec('kpCode', 'clear')
                            if (this._findRule('jpCode')) this.formObj.exec('jpCode', 'clear')
                        }
                    },
                    col: {
                        span: 6
                    }
                }, {
                    type: 'AutoCompleteQuery',
                    field: 'kpCode',
                    title: '平台名称：',
                    props: {
                        placeholder: '请选择平台或输入名称搜索',
                        fieldValue: 'code',
                        fieldLabel: 'name',
                        value: '',
                        queryFunc: (keyWord) => {
                            const opt = {
                                pageNum: 1,
                                pageSize: 20,
                                kcCode: this.formObj.getValue('kcCode'),
                                _loading: false, //不显示加载中图标
                            }
                            if (keyWord !== '') {
                                opt.keyWord = keyWord;
                            }
                            return getKpList(opt).then(res => {
                                if (res.data) {
                                    const list = res.data.map(item => ({
                                        ...item,
                                        value: item.name,
                                    }))
                                    return list
                                } else {
                                    return []
                                }
                            })
                        }
                    },
                    emit: ['select'],
                    // emitPrefix: 'kp',
                    col: {
                        span: 6
                    }
                }, {
                    type: 'select',
                    field: "glCode",
                    title: '线路名称：',
                    value: '',
                    options: [],
                    on: {
                        change: (item) => {
                            //清空公司 平台 计量点
                            const formData = this.formObj.bind();
                            formData.kcCode = '';
                            if (this._findRule('kpCode')) this.formObj.exec('kpCode', 'clear')
                            if (this._findRule('jpCode')) this.formObj.exec('jpCode', 'clear')
                        }
                    },
                    col: {
                        span: 6
                    }
                }, {
                    type: 'SubmitBtns',
                    field: 'btn',
                    emit: ['search', 'reset'],
                    col: {
                        span: 6
                    }
                }, {
                    type: 'select',
                    field: "billState",
                    title: '确认状态：',
                    value: '7',
                    options: [{
                        value: '7',
                        label: '全部',
                    }, {
                        value: '3',
                        label: '已确认',
                    }, {
                        value: '8',
                        label: '未确认',
                    }],
                    col: {
                        span: 6
                    }
                }, {
                    type: "DatePicker",
                    field: "month",
                    title: "结算月份：",
                    value: getPrevMonth(), //月份,
                    props: {
                        type: "month",
                        format: "yyyy年M月",
                        placeholder: "请选择账单月",
                        pickerOptions: {
                            disabledDate(time) {
                                return new Date(time) > new Date().getTime()
                            }
                        },
                    },
                    col: {
                        span: 6
                    }
                }, {
                    type: 'select',
                    field: "type",
                    title: '表计类型：',
                    value: '',
                    options: [{
                        value: '',
                        label: '全部',
                    }, {
                        value: '1',
                        label: '单表',
                    }, {
                        value: '2',
                        label: '共表',
                    }, {
                        value: '3',
                        label: '合表',
                    }, {
                        value: '4',
                        label: '国网',
                    }],
                    col: {
                        span: 6
                    }
                }, {
                    type: 'ElCol',
                    props: {
                        span: 6,
                    },
                }, {
                    type: 'input',
                    field: "gcAccount",
                    title: '户　　号：',
                    value: '',
                    props: {
                        placeholder: '请输入户号',
                    },
                    col: {
                        span: 6
                    }
                }]
            }
        },
        mounted() {
            this.search();
        },
        methods: {
            search() {
                const formData = this.formObj.formData();
                const opt = {
                    glCode: formData.glCode, // 线路
                    kcCode: formData.kcCode, //公司
                    kpCode: formData.kpCode, //平台
                    type: formData.type,
                    billState: formData.billState,
                    month: parseTime(formData.month, '{y}-{m}'),
                }
                for (let key in opt) {
                    if (opt[key] == '' || opt[key] == undefined) delete opt[key];
                }
                this.$emit('searchHand', opt)
            }
        }
    }
</script>