<template>
    <!--分表、国网压裂钻井 数据维护-->
    <el-dialog title="数据维护" custom-class="cusDialog" :visible.sync="show" width="5.2rem" :before-close="handleClose">
        <el-form :model="formData" :rules="rules" ref="form" class="form onlyShow">
            <div class="li">
                <div class="item">
                    <el-form-item>
                        <div class="label">公司名称：</div>
                        <div class='rt'>
                            <div class="box">{{detailData.kcName}}</div>
                        </div>
                    </el-form-item>
                </div>
            </div>
            <div class="li">
                <div class="item">
                    <el-form-item>
                        <div class="label">平台名称：</div>
                        <div class='rt'>
                            <div class="box">{{detailData.kpName}}</div>
                        </div>
                    </el-form-item>
                </div>
            </div>
            <div class="li">
                <div class="item">
                    <el-form-item>
                        <div class="label">用电单位：</div>
                        <div class='rt'>
                            <div class="box">{{detailData.sdName}}</div>
                        </div>
                    </el-form-item>
                </div>
            </div>
            <div class="li" v-if="haveDayDetail">
                <div class="item">
                    <el-form-item prop="arSnum">
                        <div class="label"><span class="important">*</span>日用明细表：</div>
                        <el-upload class="upload-demo" :action="fileUploadUrl" :headers="headers"
                            :on-success="handleChangeFive" :before-upload="beforeUpload" :on-error="error"
                            :data="params" :show-file-list="false">
                            <el-button :loading="upLoading" size="small" type="primary">{{fileList.length?'已上传':'点击上传'}}
                            </el-button>
                        </el-upload>
                    </el-form-item>
                </div>
            </div>
            <div class="li" v-if="haveDayDetail">
                <div class="item">
                    <el-form-item>
                        <div class="label">日用表总电量：</div>
                        <span v-if="fileList.length" class="unit">{{formData.drEq}} kW·h <small
                                class="notice">表底差值:{{calc}}</small></span>
                        <span v-else class="unit red">成功导入【日用明细表】后可见</span>
                    </el-form-item>
                </div>
            </div>
            <div class="li">
                <div class="item">
                    <el-form-item prop="arSnum">
                        <div class="label"><span class="important">*</span>起度：</div>
                        <el-input type="number" :min="0" @input="inputS" v-model="formData.arSnum" placeholder="请输入起度">
                        </el-input>
                    </el-form-item>
                </div>
            </div>
            <div class="li">
                <div class="item">
                    <el-form-item prop="arEnum">
                        <div class="label"><span class="important">*</span>止度：</div>
                        <el-input type="number" @blur="inputE" :min="this.formData.arEnum" v-model="formData.arEnum"
                            placeholder="请输入止度"></el-input>
                    </el-form-item>
                </div>
            </div>
            <div class="li">
                <div class="item">
                    <el-form-item prop="blRate">
                        <div class="label">倍率：</div>
                        <el-input type="number" disabled v-model="formData.blRate" placeholder="请输入倍率"></el-input>
                    </el-form-item>
                </div>
            </div>
            <div class="li" v-if="haveDayDetail">
                <div class="item">
                    <el-form-item>
                        <div class="label">电量：</div>
                        <span class="unit" :style="same">{{allEq}} kW·h</span>
                    </el-form-item>
                </div>
            </div>
            <div class="li" v-else>
                <div class="item">
                    <el-form-item>
                        <div class="label">电量：</div>
                        <span class="unit">{{allEq}} kW·h</span>
                    </el-form-item>
                </div>
            </div>
        </el-form>
        <span slot="footer" class="dialog-footer">
            <el-button type="primary" :loading="loading" @click="submit">提 交</el-button>
            <el-button @click="handleClose">关 闭</el-button>
        </span>
    </el-dialog>
</template>
<script>
    import {
        Message
    } from 'element-ui';
    import {
        billExport
    } from '@/api/archives';
    import {
        getToken
    } from '@/utils/auth'
    import {
        parseTime,
        formatNum
    } from "@/utils/index";
    import tool from "@/utils/tool";
    export default {
        props: {
            show: {
                type: Boolean,
                default: false,
            },
            detailData: {
                type: Object,
                default: () => {},
            },
            loading: Boolean,
        },
        data() {
            return {
                fileList: [],
                headers: {
                    uapAccessToken: getToken('accessToken'),
                    uapRefreshToken: getToken('refreshToken'),
                },
                upLoading: false,
                haveDayDetail: false, //是否需要导入日用明细表   只有四川页岩气需要
                rules: {
                    arSnum: [{
                        required: true,
                        message: '起度不能为空',
                        trigger: 'blur'
                    }],
                    arEnum: [{
                        required: true,
                        message: '止度不能为空',
                        trigger: 'blur'
                    }],
                    blRate: [{
                        required: true,
                        message: '倍率不能为空',
                        trigger: 'blur'
                    }],
                },
                formData: {
                    "id": '', // 主键
                    "month": "", // 结算时间
                    "arSnum": 0, // 起码
                    "arEnum": 0, // 止码
                    "blRate": 0, // 倍率
                    "extraEq": 0, // 补录
                    "drEq": 0
                },
            }
        },
        mounted() {
            this.detailData && this.init(this.detailData);
        },
        computed: {
            fileUploadUrl() {
                return billExport(this.formData.id);
            },
            useEq() { //  电量计算来的
                const sum = ((this.formData.arEnum - this.formData.arSnum).toFixed(6) * this.formData.blRate)
                    .toFixed(2);
                return formatNum(sum) < 0 ? 0 : formatNum(sum)
            },
            allEq() { //  电量计算来的
                return Math.floor(formatNum(Number(this.useEq) + Number(this.formData.extraEq)) * 1 + 0.5)
            },
            calc() {
                return Number((this.formData.drEq / this.formData.blRate).toFixed(5));
            },
            same() {
                return {
                    color: this.formData.drEq === this.allEq && this.allEq ? 'green' : 'red'
                }
            },
            params() {
                return {
                    id: this.formData.id,
                }
            }
        },
        methods: {
            parseTime,
            handleClose() {
                this.$emit('dataInputHand')
            },
            inputS(val) { //起度大于等于0
                if (~val.indexOf('0.0') && Number(val) == 0) return;
                this.formData.arSnum = val < 0 ? 0 : Number(val);
                //自动根据表码只计算止度 只有导入日明细的才有此逻辑
                if (this.haveDayDetail) {
                    this.formData.arEnum = Number(Number(this.formData.arSnum + this.calc * 1).toFixed(5));
                }
            },
            inputE(val) {
                this.formData.arEnum = this.formData.arEnum < this.formData.arSnum ? this.formData.arSnum : Number(this
                    .formData.arEnum)
            },
            input(val) {
                this.formData.extraEq = formatNum(val)
            },
            init(val) { //初始化数据
                this.fileList = [];
                this.formData.id = val.id;
                this.formData.arSnum = val.arSnum ? val.arSnum : 0;
                this.formData.arEnum = val.arEnum ? val.arEnum : 0;
                this.formData.blRate = val.blRate ? val.blRate : 0;
                this.formData.extraEq = val.extraEq ? val.extraEq : 0;
                this.formData.month = val.month;
                this.formData.sdName = val.sdName;
                this.formData.kpName = val.kpName;
                this.haveDayDetail = !tool.showStAndEtFunc(val.kcCode);

                if (val.allEq && val.kwell) { //已经导入过日用表
                    this.fileList = [{
                        name: val.kwell
                    }]
                    this.formData.drEq = val.allEq;
                } else {
                    this.fileList = [];
                    this.formData.drEq = 0;
                }
            },
            submit() {
                let opt = {};
                if (this.haveDayDetail) { // 四川页岩气 需要日明细表
                    if (this.formData.drEq !== this.allEq || !this.allEq) {
                        this.$message({
                            type: 'warning',
                            message: '导入的日用表总电量需等于总电量',
                        })
                        return;
                    }
                    opt = {
                        ...this.formData,
                        useEq: this.useEq,
                        allEq: this.allEq,
                    }
                    delete opt.drEq;
                    delete opt.kpName;
                    delete opt.month;
                    delete opt.sdName;
                } else { //不需要日明细表的
                    if (this.useEq == 0) {
                        this.$message({
                            type: 'warning',
                            message: '电量有误，请核查后重试！',
                        })
                        return;
                    }
                    opt = {
                        id: this.formData.id,
                        arSnum: this.formData.arSnum,
                        arEnum: this.formData.arEnum,
                        useEq: this.useEq,
                        allEq: this.useEq,
                    }
                }
                this.$emit('dataInputHand', opt)
            },
            // 上传回调
            handleChangeFive(res, file, fileList) {
                this.upLoading = false;
                if (res.code == 200) {
                    this.fileList = fileList;
                    this.formData.drEq = res.data.useEq;
                } else {
                    this.msg(res.msg);
                    this.fileList = [];
                }
                if (this.fileList.length >= 2) {
                    this.fileList = [this.fileList[1]];
                }
            },
            error() {
                this.upLoading = false;
            },
            msg(err) {
                this.$message.error('上传出错:' + err);
            },
            beforeUpload(file) {
                const name = file.name.toLowerCase();
                const isExcel = ['.xls', '.xlsx'].find(item => {
                    return name.indexOf(item) !== -1;
                })
                const isLt2M = file.size / 1024 / 1024 < 2;
                if (!isExcel) {
                    this.$message.error('请上传excel文件!');

                    return false;
                }
                if (!isLt2M) {
                    this.$message.error('上传文件大小不能超过 2MB!');
                    return false;
                }
                this.upLoading = true;
                return true;
            },
            // beforeRemove(file, fileList) {
            //     return false;
            // }
        },
        watch: {
            detailData(val) {
                if (!val) return;
                this.init(val);
            },
            deep: true,
            immediate: true,
        }
    }
</script>

<style scoped lang="scss">
    .form {
        .li {
            /deep/ .el-form-item {
                width: 100%;
            }

            .notice {
                position: absolute;
                right: 0;
            }

            .red {
                color: orange;
                font-size: 12px;
            }

            .success {
                color: green;
            }

            display: flex;

            .item {
                .unit {
                    margin: 0 0.15rem;
                }

                width: 100%;
                display: flex;
                line-height: 32px;
                height: 32;

                .label {
                    font-family: "PFC";
                    min-width: 1rem;
                    margin-right: 2px;
                    color: #333;
                    text-align: right;
                }

                .box {
                    width: 100%;
                    border-radius: 4px;
                    color: #666;

                    box-sizing: border-box;
                }

                .rt {
                    flex: 1;
                }

                .btns {
                    width: 100%;
                    display: flex;
                    justify-content: flex-end;

                }
            }
        }
    }
</style>