<template>
    <div>
        <!--发起审批-->
        <el-dialog title="手动确认账单（运维）" custom-class="cusDialog" :visible.sync="show" width="6rem"
            :before-close="handleClose">
            <el-form :model="data" ref="form" class="form">
                <div class="li">
                    <div class="item">
                        <div class="label">公司名称：</div>
                        <div class='rt'>
                            <div class="box">{{detailData.kcName}}</div>
                        </div>
                    </div>
                </div>
                <div class="li">
                    <div class="item">
                        <div class="label">平台名称：</div>
                        <div class='rt'>
                            <div class="box">{{detailData.kpName}}</div>
                        </div>
                    </div>
                </div>
                <div class="li">
                    <div class="item">
                        <div class="label">用电单位：</div>
                        <div class='rt'>
                            <div class="box">{{detailData.sdName}}</div>
                        </div>
                    </div>
                </div>
                <div class="li">
                    <div class="item">
                        <div class="label">结算月份：</div>
                        <div class='rt'>
                            <div class="box">{{ parseTime(new Date(detailData.month),'{y}年{m}月')}}
                            </div>
                        </div>
                    </div>
                </div>
                <div class="li">
                    <div class="item">
                        <div class="label">结算时间：</div>
                        <div class='rt'>
                            <div class="box">
                                {{parseTime(new Date(detailData.arSt),'{y}/{m}/{d} {h}:{i}')}}-
                                {{parseTime(new Date(detailData.arEt),'{y}/{m}/{d} {h}:{i}')}}
                            </div>
                        </div>
                    </div>
                </div>
                <div class="li">
                    <div class="item">
                        <div class="label">结算电量：</div>
                        <div class='rt'>
                            <div class="box">{{detailData.allEq}} kW·h</div>
                        </div>
                    </div>
                </div>
                <div class="li">
                    <div class="item">
                        <div class="label"><span class="important">*</span>确认原因：</div>
                        <div class='rt'>
                            <el-form-item>
                                <el-select v-model="data.remark" placeholder="原因">
                                    <el-option label="无需井队确认" value="无需井队确认" :key="0"></el-option>
                                    <el-option label="井队已离开" value="井队离开" :key="1"></el-option>
                                    <el-option label="井队拒绝配合" value="井队拒绝配合" :key="2"></el-option>
                                    <el-option label="其它" value="其它" :key="3"></el-option>
                                </el-select>
                            </el-form-item>
                        </div>
                    </div>
                </div>
                <div class="li">
                    <div class="item">
                        <el-form-item>
                            <div class="label"><span class="important">*</span>现场确认单：</div>
                            <div class='rt'>
                                <el-upload :class="{hide:files.length}" :action="fileUploadUrl" :headers="headers"
                                    :on-success="handleChange" :before-upload="beforeUploadFP" list-type="picture-card"
                                    :limit="1" :file-list="files">
                                    <i slot="default" class="el-icon-plus"></i>
                                    <div slot="file" slot-scope="{file}">
                                        <img v-if="isImg(file)" class="el-upload-list__item-thumbnail" :src="file.url"
                                            alt="">
                                        <div v-else class="box">
                                            <svg-icon :icon-class="getPreType(file)" class-name="logo-img">
                                            </svg-icon>
                                        </div>
                                        <span class="el-upload-list__item-actions">
                                            <span v-if="isImg(file)" class="el-upload-list__item-preview"
                                                @click="handlePictureCardPreview(file)">
                                                <i class="el-icon-zoom-in"></i>
                                            </span>
                                            <span class="el-upload-list__item-delete" @click="handleRemove(files,file)">
                                                <i class="el-icon-delete"></i>
                                            </span>
                                        </span>
                                    </div>
                                </el-upload>
                                <p>温馨提示：需上传盖章后的现场确认单照片</p>
                                <UploadProgress ref="uploadPreogress" />
                            </div>
                        </el-form-item>
                    </div>
                </div>

            </el-form>
            <span slot="footer" class="dialog-footer">
                <el-button type="primary" :loading="loading" @click="submit">确 认</el-button>
                <el-button @click="handleClose">关 闭</el-button>
            </span>
        </el-dialog>
    </div>
</template>
<script>
    import {
        Message
    } from 'element-ui';
    import {
        getToken
    } from '@/utils/auth'
    import {
        mapState
    } from 'vuex';
    import {
        fileUploadUrl,
        resourceUrl,
    } from '@/api/archives';
    import tool from '@/utils/tool'
    import {
        parseTime
    } from "@/utils/index";
    import UploadProgress from "@/components/UploadProgress.vue"; //进度条
    export default {
        components: {
            UploadProgress,
        },
        props: {
            show: {
                type: Boolean,
                default: false,
            },
            detailData: {
                type: Object,
                default: () => {},
            },
            loading: Boolean
        },
        data() {
            return {
                fileUploadUrl, //上传文件地址,日用明细
                files: [], //上传发票文件
                headers: {
                    uapAccessToken: getToken('accessToken'),
                    uapRefreshToken: getToken('refreshToken'),
                },
                fileUploadUrlGw: '', //上传日用明细地址
                data: {
                    remark: '无需井队确认'
                },
            }
        },
        computed: {
            ...mapState({
                userId: state => state.user.userId, //当前登录的user
            })
        },
        methods: {
            parseTime,
            //模拟发票
            handleChange(res, file, fileList) {
                if (res.code == 200) {
                    this.files = fileList.slice(0);
                    this.$refs.uploadPreogress.success();
                } else {
                    this.msg(res.msg);
                    this.files = [];
                }
            },
            isImg(file) {
                const name = file.name.toLowerCase();
                return ['.png', '.jpeg', '.jpg'].find(item => {
                    return name.indexOf(item) !== -1;
                })
            },
            //删除图片
            handleRemove(data, file) {
                let index = 0;
                data.map((item, index) => {
                    if (item.name === file.name) {
                        index = index;
                    }
                })
                data.splice(index, 1)
            },
            //预览图片
            handlePictureCardPreview(file) {
                this.$viewerApi({
                    images: [file.url],
                })

            },
            //获取保存的url地址
            _getUrl(item) {
                if (!item) return '';
                if (item.response) {
                    return item.response.fileName;
                } else if (item.url) {
                    return item.url.replace(baseProfileUrl, '');
                }
                return '';
            },
            //模拟发票格式验证
            beforeUploadFP(file) {
                if (this.$refs.uploadPreogress.isLoading()) return false;
                const name = file.name.toLowerCase();
                const isPngOrPdf = ['.png', '.jpeg', '.jpg', '.pdf'].find(item => {
                    return name.indexOf(item) !== -1;
                })
                const isLt2M = (file.size / 1024 / 1024) < 2;
                if (!isPngOrPdf) {
                    this.$message.error('请上传png、jpeg、jpg或pdf文件!');
                    return false;
                }

                this.$refs.uploadPreogress.start(); //开始增加进度

                //如果时图片且超过2M 压缩图片
                if (!isLt2M && !~name.indexOf('.pdf')) {
                    return tool.compressImg(file);
                } else {
                    return true;
                }
            },
            handleClose() {
                this.$emit('confirmHand')
            },
            submit() {
                if (!this.files[0]) {
                    if (!this.$refs.uploadPreogress.isLoading()) { //还没上传模拟发票
                        this.$message.warning('请上传模拟发票');
                    } else { //上传到一半
                        this.$message.warning('模拟发票正在上传，请稍后再试');
                    }
                    return;
                }
                this.$emit('confirmHand', {
                    id: this.detailData.id,
                    userId: this.userId,
                    remark: this.data.remark,
                    sitePaperFile: this._getUrl(this.files[0]),
                    parentId: this.detailData.parentId
                })
                //初始化
                this.data.remark = "无需井队确认";
                this.files = []

            }
        },
    }
</script>

<style scoped lang="scss">
    .form {
        .li {
            &:last-child {
                margin-bottom: 0;
            }

            /deep/ .el-select {
                width: 100%;
            }

            display: flex;
            margin-bottom: 22px;

            .item {
                width: 100%;
                display: flex;
                line-height: 32px;
                height: 32;

                .label {
                    font-family: "PFC";
                    min-width: 0.5rem;
                    margin-right: 2px;
                    color: #333;
                    text-align: right;
                }

                .box {
                    width: 100%;
                    border-radius: 4px;
                    color: #666;
                    padding: 0 10px;
                    box-sizing: border-box;
                }

                .rt {
                    flex: 1;

                    /deep/ .el-form-item {
                        margin-bottom: 0
                    }
                }

                .btns {
                    width: 100%;
                    display: flex;
                    justify-content: flex-end;

                }
            }
        }

        /deep/ .hide .el-upload--picture-card {
            display: none;
        }
    }
</style>