<!--账单查看和维护-->
<template>
    <div>
        <div class="content-title">该条记录包含的账单如下：</div>
        <el-table row-key="index" v-table-scroll :data="tableData" @selection-change="handleSelectionChange"
            :header-cell-style="{background:'#E6EAFE',color:'#606266'}" size="small" height="200">
            <el-table-column type="selection" width="55"></el-table-column>
            <el-table-column label="序号" type="index" width="70" />
            <el-table-column label="所属平台" prop="kpName" width="200"></el-table-column>
            <el-table-column label="用电性质" prop="workType" width="90"></el-table-column>
            <el-table-column label="账单月份" prop="arSnum" width="120">
                <template slot-scope="scope">
                    <span>{{ changeMonth(scope.row['month']) }}</span>
                </template>
            </el-table-column>
            <el-table-column label="起度" prop="arSnum" width="100"></el-table-column>
            <el-table-column label="止度" prop="arEnum" width="100"></el-table-column>
            <el-table-column label="倍率" prop="blRate" width="80"></el-table-column>
            <el-table-column label="电量（kw·h）" prop="useEq" width="120"></el-table-column>
            <el-table-column label="分摊损耗比例" prop="loseRate" width="120"></el-table-column>
            <el-table-column label="损耗电量" prop="loseEq" width="100"></el-table-column>
            <el-table-column label="补录电量" prop="extraEq" width="100"></el-table-column>
            <el-table-column label="总电量（kw·h）" prop="allEq" width="130"></el-table-column>
            <el-table-column label="所属公司" prop="kcName" show-overflow-tooltip width="200"></el-table-column>
            <el-table-column label="所属路线" prop="glName" width="200" show-overflow-tooltip></el-table-column>
            <el-table-column label="计量点名称" prop="jpName" show-overflow-tooltip width="250"></el-table-column>
            <el-table-column label="用能单位" prop="sdName" show-overflow-tooltip width="240"></el-table-column>
            <el-table-column label="结算起止码时间" prop="ts" width="300">
                <template slot-scope="scope">
                    <span v-if="scope.row['arSt']">
                        {{changeTime(scope.row['arSt'])}}-{{changeTime(scope.row['arEt'])}}
                    </span>
                </template>
            </el-table-column>
        </el-table>
        <div class="page pagination-box">
            <div v-if="selectedRows.length" class="notice">已选择 <strong
                    class='selecedNumber'>{{selectedRows.length}}</strong> 条记录</div>
            <div v-else class="notice warning">请从上面表格勾选出本月暂不结算的账单</div>
            <el-pagination small @current-change="handleCurrentChange" :current-page="page.pageNum"
                :page-size="page.pageSize" layout="total" :total="total">
            </el-pagination>
        </div>
        <div v-show="selectedRows.length">
            <el-form :model="selectedObj" :rules="rules" ref="form" class="form">
                <el-row :gutter="100">
                    <el-col :span="17">
                        <el-form-item prop="reason">
                            <div class="label"><span class="important">*</span>暂不结算原因：</div>
                            <el-input class="textarea" type="textarea" v-model="selectedObj.reason"
                                placeholder="请输入暂不结算原因" :autosize="{ minRows: 3, maxRows:10}"></el-input>
                        </el-form-item>
                    </el-col>
                    <el-col :span="7" class="item">
                        <el-button type="primary" @click="submit">提 交</el-button>
                    </el-col>
                </el-row>
            </el-form>
        </div>
    </div>
</template>
<script>
    import {
        parseTime
    } from "@/utils/index";
    import {
        getListBillByFinalId,
        postRemoveBill
    } from "@/api/archives.js";
    import {
        mapState
    } from 'vuex';
    import Table from "@/components/TableCommon.vue";
    import FormCreateCommon from "@/components/TableCommon.vue";
    export default {
        extends: Table,
        name: 'editBills',
        props: {
            billId: "",
        },
        data() {
            return {
                // tableData: [],
                selectedRows: [],
                selectedObj: {},
                rules: {
                    reason: [{
                        required: true,
                        message: '原因不能为空',
                        trigger: 'blur'
                    }],
                }
            }
        },
        mounted() {
            if (this.billId) this.getById();
        },
        computed: {
            ...mapState({
                userId: state => state.user.userId, //当前登录的user
            })
        },
        methods: {
            submit() {
                this.$refs['form'].validate((valid) => {
                    if (valid) {
                        postRemoveBill({
                            finalBillId: this.billId,
                            userId: this.userId,
                            paramList: this.selectedRows.map(li => {
                                return {
                                    billId: li.id,
                                    reason: this.selectedObj.reason,
                                }
                            })
                        }).then(res => {
                            this.$message.success('操作成功');
                            this.selectedRows = [];
                            this.getById();
                            this.reset();
                            this.$emit('reloadTable') //刷新表格数据
                        })

                    } else {
                        return false;
                    }
                });
            },
            //选择已绑定的记录
            handleSelectionChange(val) {
                this.selectedRows = val;
            },
            changeMonth(t) {
                return parseTime(new Date(t), '{y}年{m}月')
            },
            changeTime(t) {
                return parseTime(new Date(t), '{y}/{m}/{d} {h}:{i}')
            },
            getById(id) {
                if (!id) id = this.billId;
                getListBillByFinalId(id, {
                    // pageNum: 1,
                    // pageSize: 1,
                }).then(res => {
                    this.tableData = res.rows ? res.rows : [];
                    this.total = res.total;
                })
            },
            reset() {
                this.selectedObj.reason = '';
            }
        },
        watch: {
            billId: {
                handler(id) {
                    if (id) this.getById(id);
                }
            }
        }
    }
</script>

<style scoped lang="scss">
    .page {
        position: relative;
        height: 60px;

        .selecedNumber {
            color: #2E4AF7;
        }

        .notice {
            position: absolute;

        }
    }

    .label {
        width: 140px;
    }

    .item {
        text-align: right;
    }
</style>