<template>
    <!--分表数据维护-->
    <el-dialog title="分表数据维护" custom-class="cusDialog" :visible.sync="show" width="4.2rem" :before-close="handleClose">
        <el-form :model="formData" :rules="rules" ref="form" class="form">
            <div class="li">
                <div class="item">
                    <el-form-item prop="arSnum">
                        <div class="label"><span class="important">*</span>起度：</div>
                        <el-input type="number" :min="0" @input="inputS" v-model="formData.arSnum" placeholder="请输入起度">
                        </el-input>
                    </el-form-item>
                </div>
            </div>
            <div class="li">
                <div class="item">
                    <el-form-item prop="arSnum">
                        <div class="label"><span class="important">*</span>止度：</div>
                        <el-input type="number" @blur="inputE" :min="this.formData.arSnum" v-model="formData.arEnum"
                            placeholder="请输入止度"></el-input>
                    </el-form-item>
                </div>
            </div>
            <div class="li">
                <div class="item">
                    <el-form-item prop="blRate">
                        <div class="label"><span class="important">*</span>倍率：</div>
                        <el-input type="number" disabled v-model="formData.blRate" placeholder="请输入倍率"></el-input>
                    </el-form-item>
                </div>
            </div>
            <div class="li">
                <div class="item">
                    <el-form-item>
                        <div class="label"><span class="important">*</span>电量：</div>
                        <el-input v-model="useEq" disabled>
                            <template slot="append">kW·h</template>
                        </el-input>
                    </el-form-item>
                </div>
            </div>
            <div class="li">
                <div class="item">
                    <el-form-item>
                        <div class="label">补录电量：</div>
                        <el-input type="number" @input="input" v-model="formData.extraEq" placeholder="请输入补录电量">
                            <template slot="append">kW·h</template>
                        </el-input>
                    </el-form-item>
                </div>
            </div>
            <div class="li">
                <div class="item">
                    <el-form-item>
                        <div class="label"><span class="important">*</span>总电量：</div>
                        <el-input v-model="allEq" disabled>
                            <template slot="append">kW·h</template>
                        </el-input>
                    </el-form-item>
                </div>
            </div>

        </el-form>
        <span slot="footer" class="dialog-footer">
            <el-button type="primary" :loading="loading" @click="submit">提 交</el-button>
            <el-button @click="handleClose">关 闭</el-button>
        </span>
    </el-dialog>
</template>
<script>
    import {
        Message
    } from 'element-ui';

    import {
        parseTime,
        formatNum
    } from "@/utils/index";
    export default {
        props: {
            show: {
                type: Boolean,
                default: false,
            },
            detailData: {
                type: Object,
                default: () => {},
            },
            loading: Boolean,
        },
        data() {
            return {
                rules: {
                    arSnum: [{
                        required: true,
                        message: '起度不能为空',
                        trigger: 'blur'
                    }],
                    arEnum: [{
                        required: true,
                        message: '止度不能为空',
                        trigger: 'blur'
                    }],
                    blRate: [{
                        required: true,
                        message: '倍率不能为空',
                        trigger: 'blur'
                    }],
                },
                formData: {
                    "id": '', // 主键
                    "month": "", // 结算时间
                    "arSnum": 0, // 起码
                    "arEnum": 0, // 止码
                    "blRate": 0, // 倍率
                    "extraEq": 0, // 补录
                }
            }
        },
        mounted() {
            this.detailData && this.init(this.detailData);
        },
        computed: {
            useEq() { //  电量计算来的
                const sum = ((this.formData.arEnum - this.formData.arSnum).toFixed(6) * this.formData.blRate)
                    .toFixed(2);
                return formatNum(sum)
            },
            allEq() { //  电量计算来的
                return formatNum(Number(this.useEq) + Number(this.formData.extraEq))
            },
        },
        methods: {
            parseTime,
            handleClose() {
                this.$emit('dataInputHand')
            },
            inputS(val) { //起度大于等于0
                this.formData.arSnum = val < 0 ? 0 : Number(val);
            },
            inputE(val) {
                this.formData.arEnum = this.formData.arEnum < this.formData.arSnum ? this.formData.arSnum : Number(this
                    .formData.arEnum)
            },
            input(val) {
                this.formData.extraEq = formatNum(val)
            },
            init(val) { //初始化数据
                this.formData.id = val.id;
                this.formData.arSnum = val.arSnum ? val.arSnum : 0;
                this.formData.arEnum = val.arEnum ? val.arEnum : 0;
                this.formData.blRate = val.blRate ? val.blRate : 0;
                this.formData.extraEq = val.extraEq ? val.extraEq : 0;
                this.formData.month = val.month;
            },
            submit() {
                this.$emit('dataInputHand', {
                    ...this.formData,
                    useEq: this.useEq,
                    allEq: this.allEq,
                })
            },
        },
        watch: {
            detailData(val) {
                if (!val) return;
                this.init(val);
            },
            deep: true,
            immediate: true,
        }
    }
</script>

<style scoped lang="scss">
    .form {
        .li {
            /deep/ .el-form-item {
                width: 100%;
            }

            display: flex;

            .item {
                width: 100%;
                display: flex;
                line-height: 32px;
                height: 32;

                .label {
                    font-family: "PFC";
                    min-width: 0.8rem;
                    margin-right: 2px;
                    color: #333;
                    text-align: right;
                }

                .box {
                    width: 100%;
                    border-radius: 4px;
                    color: #666;
                    padding: 0 10px;
                    box-sizing: border-box;
                }

                .rt {
                    flex: 1;
                }

                .btns {
                    width: 100%;
                    display: flex;
                    justify-content: flex-end;

                }
            }
        }
    }
</style>