<!--账单生成-->
<template>
    <div>
        <SearchInputs @downHand="downHand" @searchHand="searchHand"></SearchInputs>
        <CusTable :tableData="tableData" @btnClick="btnClick" @handleSizeChange="handleSizeChange"
            @handleCurrentChange="handleCurrentChange" :pageNum="page.pageNum" :pageSize="page.pageSize" :total="total">
        </CusTable>

        <!--审核-->
        <ExamModel :show="examModelVisible" :detailData="detailData" :loading="loading" @examHand="examHand">
        </ExamModel>

        <!--异常处理-->
        <ErrorDlModel :show="errorModelVisible" :detailData="detailData" :loading="loading" @errorHand="errorHand"
            :type="1">
        </ErrorDlModel>

        <!--日用明细数据维护-->
        <SubTableDataInput :show="dataInputModelVisible" :detailData="detailData" :loading="loading"
            @dataInputHand="dataInputHand">
        </SubTableDataInput>

        <!--编辑数据弹窗-->
        <BaseEditModel :show="editModelVisible" :type="editModelType" :detailData="detailData" :loading="loading"
            @editHand="editHand">
        </BaseEditModel>

        <!--弹框 日用电明细 现场电量确认单-->
        <el-dialog :title="tableDialogType==2?'现场电量确认单':'日用电明细'" custom-class="cusDialog"
            :visible.sync="showTableDialog" width="10rem" :before-close="handleClose" @closed="closed">
            <div class="print-body">
                <TempalteBill v-if="showPrintWd&&tableDialogType==1" :type="4" :tableData="detailData"></TempalteBill>
                <TempalteBill2 v-if="showPrintWd&&tableDialogType==2" :type="1" :tableData="detailData"></TempalteBill2>
            </div>
            <span slot="footer" class="dialog-footer">
                <el-button type="primary" @click="html2Excel">下 载</el-button>
                <el-button type="primary" v-print="printObj">打 印</el-button>
                <el-button @click="handleClose">取 消</el-button>
            </span>
        </el-dialog>
    </div>
</template>
<script>
    //组件
    import TableCommon from "@/components/TableCommon.vue";
    import CusTable from "./components/billCreate/BillCreateTable.vue";
    import SearchInputs from "./components/billCreate/SearchInputsCreate.vue";
    import ExamModel from "./components/billCreate/ExamModel.vue"; //审核弹框
    import ErrorDlModel from "./components/common/ErrorDlModel.vue"; //审核弹框
    import SubTableDataInput from "./components/billCreate/SubTableDataInput.vue"; //数据维护
    import BaseEditModel from "./components/billCreate/BaseEditModel.vue"; //编辑名称
    import TempalteBill from "@/components/tempalteBill.vue"; //打印
    import {
        parseTime
    } from "@/utils/index";
    //接口
    import {
        getComfirmUnApplyList, //账单生成列表
        postComfirmApply, //发起审核
        postBillException, //账单异常
        postBillUpdate, //账单维护
        exportBill, //账单导出
        getEditWell,
        postStateGridFile, //国网数据保存

    } from "@/api/archives.js";
    //公用函数
    import tool from "@/utils/tool";
    import {
        mapState
    } from 'vuex';
    import Table2Excel from 'table2excel.js'
    export default {
        extends: TableCommon,
        name: 'BillCreate',
        components: {
            CusTable,
            SearchInputs,
            ExamModel, //审核弹框
            ErrorDlModel, //异常处理
            SubTableDataInput, //数据维护
            TempalteBill, //打印
            BaseEditModel, //编辑名称
            TempalteBill2: TempalteBill, //打印
        },
        data() {
            return {
                //表单
                examModelVisible: false, //审核弹框
                errorModelVisible: false, //异常处理弹框
                dataInputModelVisible: false, //  日用明细数据维护
                editModelType: 1, //编辑弹框的类型 1：井队名称修改 2：国网数据修改
                editModelVisible: false, //编辑弹框
                showTableDialog: false, // 打印
                tableDialogType: 1, //打印表单类型

                detailData: {}, //单条账单详细数据
                showPrintWd: false, //打印组件控制
                printObj: { //打印参数
                    id: "printArea",
                    popTitle: '　',
                }
            }
        },
        created() {

        },
        computed: {
            ...mapState({
                userId: state => state.user.userId, //当前登录的user
            })
        },
        mounted() {
            //获取数据
            //this._getData();
        },
        methods: {
            parseTime,
            isYes: tool.isYes,
            _getData(params = {}) { // 通过参数 获取最新数据 如果无参数 则是第一次加载
                params = {
                    ...this.search,
                    ...this.page,
                    billState: '',
                }
                getComfirmUnApplyList(params).then(res => {
                    const data = res.rows ? res.rows : [];
                    //处理数据序号
                    data.forEach((item, index) => {
                        item.index = (this.page.pageNum - 1) * this.page.pageSize + index + 1;
                        if (item.type == 4) { //国网账单 加特殊标识
                            item.index += ' 【国】';
                        }
                        if (item.subBills && item.subBills.length) {
                            item.subBills.forEach((p, k) => {
                                p.index = item.index + '.' + (k + 1);
                                p.isChild = true;
                            })
                        }
                    });
                    this.tableData = data;
                    this.total = res.total;
                })
            },
            //导出按钮
            downHand() {
                exportBill({
                    ...this.search,
                    billState: '1',
                }).then(tool.down)
            },
            btnClick(name, row) {
                this.detailData = {
                    ...row
                };
                if (name == "发起审核") {
                    this.examModelVisible = true;
                } else if (name == "异常处理") {
                    // this.$message.warning('该功能正在维护中，敬请期待')
                    // return;
                    this.errorModelVisible = true;
                } else if (name == "数据维护") { //一类是日用明细表弹出 一类是传入模拟发票
                    if (this.isYes(row.stateGirdPaperImp)) { //发票上传 分两类 2：编辑国网数据
                        this.editModelType = 2;
                        this.editModelVisible = true;
                    } else if (this.isYes(row.dayPaperImp)) { //日用明细导入
                        this.dataInputModelVisible = true;
                    }
                    //分表倍率等于总表倍率
                } else if (name == '日用电明细') {
                    this.showPrintWd = true;
                    this.tableDialogType = 1; //日用电明细表
                    this.showTableDialog = true;

                } else if (name == '现场电量确认单') {
                    this.showPrintWd = true;
                    this.tableDialogType = 2; //现场电量确认单
                    this.showTableDialog = true;

                } else if (name == '编辑名称') { //编辑弹框 分两类 1：编辑名称 2：编辑国网数据
                    this.editModelType = 1;
                    this.editModelVisible = true;
                }
            },
            handleClose() {
                this.showTableDialog = false;
            },
            //异常处理
            errorHand(row, data) {
                if (!row) {
                    this.errorModelVisible = false;
                    return;
                }
                this.loading = true;
                postBillException({
                    billId: row.id,
                    userId: this.userId,
                    solveType: data.solveType,
                    reason: data.reason,
                    date: this.parseTime(new Date()),
                }).then(res => {
                    this.loading = false; //停止加载
                    this.errorModelVisible = false; //关闭弹窗
                    this.resPublick(res);
                })
            },
            editHand(data, type) {
                if (!data) {
                    this.editModelVisible = false;
                    return;
                }
                this.loading = true;
                if (type == 1) {
                    getEditWell({
                        id: this.detailData.id,
                        wellName: data.kwell,
                    }).then(res => {
                        this.loading = false; //停止加载
                        this.editModelVisible = false;
                        this.resPublick(res);
                    })
                } else {
                    postStateGridFile(data).then(res => {
                        this.loading = false; //停止加载
                        this.editModelVisible = false;
                        this.resPublick(res);
                    })
                }
            },
            //发起审核
            examHand(row) {
                if (!row) {
                    this.examModelVisible = false;
                    return;
                }
                //检查子账单电量之和是否等于总账单
                if (row.subBills && row.subBills.length) {
                    const all = row.subBills.length == 1 ? row.subBills[0].allEq : row.subBills.reduce((a, b) => {
                        return a + b.allEq
                    }, 0)
                    if (all !== row.allEq) {
                        this.$message({
                            type: 'warning',
                            message: '子帐单电量之和不等于总账单电量，请完善后重试'
                        })
                        return;
                    }
                }
                this.loading = true;
                postComfirmApply({
                    userId: this.userId,
                    id: row.id,
                }).then(res => {
                    this.loading = false; //停止加载  
                    this.examModelVisible = false; //关闭弹窗
                    this.resPublick(res);
                }).catch(err => {
                    setTimeout(() => {
                        this.loading = false; //停止加载
                    }, 1000);
                })
            },
            //数据维护
            dataInputHand(data) {
                if (!data) {
                    this.dataInputModelVisible = false;
                    return;
                }
                if (this.detailData.parentId) { //国网钻井压裂上传日用明细表不需要判断子表电量是否大于父表
                    //判断提交的分表数据是否大于总表电量
                    const parentData = this.tableData.find(item => item.id == this.detailData.parentId)
                    if (data.allEq > parentData.allEq) {
                        this.$message.warning('分表电量不应大与总表电量，请核查')
                        return;
                    }
                }
                this.loading = true;
                //国网压裂钻井的数据维护
                if (this.detailData.type == 4) {
                    postStateGridFile(data).then(res => {
                        this.loading = false; //停止加载
                        this.dataInputModelVisible = false;
                        this.resPublick(res);
                    }).catch(err => {
                        this.loading = false; //停止加载
                    })
                } else { //普通共表数据维护
                    postBillUpdate(data).then(res => {
                        this.loading = false; //停止加载
                        this.dataInputModelVisible = false; //关闭弹窗
                        this.resPublick(res);
                    }).catch(err => {
                        this.loading = false; //停止加载
                    })
                }
            },
            //下载excel
            html2Excel() {
                const name = this.detailData.kwell ? this.detailData.kwell : this.detailData.kpName;
                new Table2Excel('#printArea table').export(
                    `${name}-${this.detailData.month}-${this.tableDialogType==1?'日用电明细表':'现场电量确认单'}`);
            },
            //打印
            closed() {
                this.showPrintWd = false;
            },
            //统一的接口返回提示接口
            resPublick(res) {
                this.loading = false; //停止加载
                this.$message({
                    type: 'success',
                    message: '操作成功'
                })
                this.reloadTable();
            },
        }
    }
</script>