<!--账单查询-->
<template>
    <div style="margin-top:0.24rem">
        <InfoBox :items="infoIems" title="账单明细"></InfoBox>
        <SearchInputs @downHand="downHand" @searchHand="searchHand"></SearchInputs>

        <CusTable :tableData="tableData" @btnClick="btnClick" @handleSizeChange="handleSizeChange"
            @handleCurrentChange="handleCurrentChange" :pageNum="page.pageNum" :pageSize="page.pageSize" :total="total">
        </CusTable>
        <!--弹框 现场电量确认单-->
        <el-dialog title="现场电量确认单" custom-class="cusDialog" :visible.sync="showTableDialog" width="10rem"
            :before-close="handleClose" @closed="closed">
            <!--弹框  流程-->
            <el-dialog width="6.8rem" title="流程跟踪表（当前节点）" :visible.sync="showTableFlowDialog" append-to-body>
                <BillFlow :id="billId" :sitePaperFile="detailData.sitePaperFile"></BillFlow>
                <span slot="footer" class="dialog-footer">
                    <el-button @click="closeFlowDialog">关 闭</el-button>
                </span>
            </el-dialog>
            <!--弹框  流程-->

            <!--弹框  打印-->
            <div class="print-body">
                <TempalteBill v-if="showPrintWd" :type="1" :tableData="detailData"></TempalteBill>
            </div>
            <span slot="footer" class="dialog-footer">
                <el-button v-show="haveFp" @click="showFp">模拟发票</el-button>
                <el-button type="primary" @click="html2Excel">下 载</el-button>
                <el-button type="primary" v-print="printObj">打 印</el-button>
                <el-button @click="showBillFlow">查看流程</el-button>
                <el-button @click="handleClose">取 消</el-button>
            </span>
        </el-dialog>
    </div>
</template>
<script>
    //组件
    import TableCommon from "@/components/TableCommon.vue";
    import CusTable from "./components/billSearch/BillTable.vue";
    import SearchInputs from "./components/billSearch/SearchInputs.vue";
    import InfoBox from "@/components/InfoBox.vue";
    import TempalteBill from "@/components/tempalteBill.vue";
    import BillFlow from "./components/common/BillFlow.vue";
    import {
        parseTime
    } from "@/utils/index";
    //接口
    import {
        getBillList, //账单查询
        getBillStatistics, //账单分类统计
        exportBill, //
    } from "@/api/archives.js";
    import {
        baseProfileUrl
    } from '@/api/baseUrl.js';
    import Table2Excel from 'table2excel.js'
    //公用函数
    import tool from "@/utils/tool.js";
    import {
        mapState
    } from 'vuex';
    export default {
        extends: TableCommon,
        name: 'BillSearch',
        components: {
            CusTable,
            SearchInputs,
            InfoBox,
            TempalteBill,
            BillFlow,
        },
        data() {
            return {
                infoIems: [],
                //表单
                showTableDialog: false, //打印
                showTableFlowDialog: false, //流程
                detailData: {}, //单条账单详细数据
                billId: '', //账单
                showPrintWd: false, //控制打印组件销毁和显示
                printObj: { //打印参数
                    id: "printArea",
                    popTitle: '　',
                },
                haveFp: false, //是否显示发票预览
            }
        },
        computed: {
            ...mapState({
                userId: state => state.user.userId, //当前登录的user
            })
        },
        mounted() {

        },
        methods: {
            isYes: tool.isYes,
            parseTime,
            _getData(params = {}) { // 通过接口 获取最新数据 如果无接口 则是第一次加载
                params = {
                    ...this.search,
                    ...this.page,
                }
                getBillList(params).then(res => {
                    const data = res.data ? res.data.billData.rows : [];
                    //处理数据序号
                    data.forEach((item, index) => {
                        item.index = (this.page.pageNum - 1) * this.page.pageSize + index + 1;
                        if (item.type == 4) { //国网账单 加特殊标识
                            item.index += ' 【国】';
                        }
                        if (item.subBills && item.subBills.length) {
                            item.subBills.forEach((p, k) => {
                                p.index = item.index + '.' + (k + 1);
                                p.isChild = true;
                            })
                        }
                    });
                    this.tableData = data;
                    this.total = res.data.billData.total;
                    this.getCalc(res.data.statistics);
                })
            },
            //显示统计栏
            getCalc(data) {
                this.infoIems = [{
                    label: '当前账单总数',
                    num: data.total ? data.total : 0,
                }, {
                    label: '未发起审核账单数',
                    num: data.notApply ? data.notApply : 0,
                }, {
                    label: '待审核账单数',
                    num: data.notCheck ? data.notCheck : 0,
                }, {
                    label: '待市场确认账单数',
                    num: data.notDone ? data.notDone : 0,
                }, {
                    label: '完结账单数',
                    num: data.done ? data.done : 0,
                }, {
                    label: '异常账单数',
                    num: data.exception ? data.exception : 0,
                }]
            },
            showFp() { //发票预览
                let previewUrl = baseProfileUrl + this.detailData.stateGridPaper;
                if (!this.detailData.stateGridPaper) {
                    this.$message.warning('暂无图片')
                    return;
                }
                this.$viewerApi({
                    images: [previewUrl],
                })
            },
            //下载excel
            html2Excel() {
                const name = this.detailData.kwell ? this.detailData.kwell : this.detailData.kpName;
                new Table2Excel('#printArea table').export(
                    `${name}-${this.detailData.month}-现场电量确认单`);
            },
            closed() {
                this.showPrintWd = false;
            },
            //导出按钮
            downHand() {
                exportBill({
                    ...this.search,
                }).then(tool.down)
            },

            btnClick(name, row) {
                this.detailData = row;
                this.haveFp = this.isYes(row.stateGirdPaperImp);
                if (name == "查看") {
                    this.showPrintWd = true;
                    this.showTableDialog = true;
                }
            },
            handleClose() {
                this.showTableDialog = false;
            },
            //查看账单流程
            showBillFlow() {
                this.billId = this.detailData.id;
                this.showTableFlowDialog = true;
            },
            closeFlowDialog() {
                this.showTableFlowDialog = false;
            },
            //统一的接口返回提示接口
            resPublick(res) {
                this.loading = false; //停止加载
                this.$message({
                    type: 'success',
                    message: '操作成功'
                })
                this.reloadTable();
            },
        }
    }
</script>