<!--账单生成-->
<template>
    <div>
        <SearchInputs @downHand="downHand" @searchHand="searchHand"></SearchInputs>
        <CusTable :tableData="tableData" @btnClick="btnClick" @handleSizeChange="handleSizeChange"
            @handleCurrentChange="handleCurrentChange" :pageNum="page.pageNum" :pageSize="page.pageSize" :total="total">
        </CusTable>


        <!--分表数据处理-->
        <SubTableDataInput :show="this.dataInputModelVisible" :detailData="detailData" :loading="loading"
            @dataInputHand="dataInputHand">
        </SubTableDataInput>

        <!--弹框 现场电量确认单-->
        <el-dialog :title="tableDialogType==1?'日用电明细':'现场电量确认单'" custom-class="cusDialog"
            :visible.sync="showTableDialog" width="10rem" :before-close="handleClose" @closed="closed">
            <!--弹框  流程-->
            <el-dialog width="6.8rem" title="流程跟踪表（当前节点）" :visible.sync="showTableFlowDialog" append-to-body>
                <BillFlow :id="billId" :sitePaperFile="detailData.sitePaperFile"></BillFlow>
                <span slot="footer" class="dialog-footer">
                    <el-button @click="closeFlowDialog">关 闭</el-button>
                </span>
            </el-dialog>

            <!--弹框  流程-->
            <div class="print-body">
                <TempalteBill v-if="showPrintWd&&tableDialogType==1" :type="4" :tableData="detailData"></TempalteBill>
                <TempalteBill2 v-if="showPrintWd&&tableDialogType==2" :type="1" :tableData="detailData"></TempalteBill2>
            </div>
            <span slot="footer" class="dialog-footer">
                <el-button v-show="haveFp" @click="showFp">模拟发票</el-button>
                <el-button type="primary" @click="html2Excel">下 载</el-button>
                <el-button type="primary" v-print="printObj">打 印</el-button>
                <el-button @click="showBillFlow">查看流程</el-button>
                <el-button @click="handleClose">取 消</el-button>
            </span>
        </el-dialog>
        <!--异常处理-->
        <ExceptionDo :show="this.exceptionDoDialog" :detailData="detailData" @exceptionHand="exceptionHand"
            :type="solveType"></ExceptionDo>

        <!--流转选择结算单-->
        <SelectBill :show="this.selectBillDialog" :detailData="detailData" @selectHand="selectHand"></SelectBill>
    </div>
</template>
<script>
    //组件
    import TableCommon from "@/components/TableCommon.vue";
    import CusTable from "./components/exceptionList/BillTable.vue";
    import SearchInputs from "./components/billCreate/SearchInputsCreate.vue";
    import BillFlow from "./components/common/BillFlow.vue";
    import SubTableDataInput from "./components/exceptionList/SubTableDataInput.vue"; //数据维护
    import ExceptionDo from "./components/exceptionList/ExceptionDo.vue"; //异常处理
    import SelectBill from "./components/exceptionList/SelectBill.vue"; //异常处理
    import TempalteBill from "@/components/tempalteBill.vue"; //打印
    import {
        parseTime
    } from "@/utils/index";
    //接口
    import {
        getExceptionList, //账单生成列表
        postBillUpdate, //账单维护
        postExceptionDo, //账单流转
        postExceptionSolve, //异常处理
        exportBill,
        postAddToFinalBill,
    } from "@/api/archives.js";
    import {
        baseProfileUrl
    } from '@/api/baseUrl.js';
    //公用函数
    import tool from "@/utils/tool";
    import Table2Excel from 'table2excel.js'
    import {
        mapState
    } from 'vuex';
    export default {
        extends: TableCommon,
        name: 'ExceptionList',
        components: {
            CusTable,
            SearchInputs,
            BillFlow,
            SubTableDataInput, //数据维护
            ExceptionDo, //异常处理
            TempalteBill, //打印
            TempalteBill2: TempalteBill, //打印
            SelectBill, // 选择结算单
        },
        data() {
            return {
                //表单
                dataInputModelVisible: false, //分表数据维护

                showTableDialog: false, //现场电量确认单查看弹框 
                showTableFlowDialog: false, // 
                exceptionDoDialog: false, // 异常处理弹框
                selectBillDialog: false, //选择结算单弹窗
                billId: '',
                detailData: {}, //单条账单详细数据

                solveType: '1', //异常状态
                tableDialogType: 1, //打印表单类型
                showPrintWd: false, //控制打印组件销毁和显示
                printObj: { //打印参数
                    id: "printArea",
                    popTitle: '　',
                },
                haveFp: false, //是否显示发票预览
            }
        },
        computed: {
            ...mapState({
                userId: state => state.user.userId, //当前登录的user
            })
        },
        mounted() {

        },
        methods: {
            parseTime,
            isYes: tool.isYes,
            _getData(params = {}) { // 通过接口 获取最新数据 如果无接口 则是第一次加载
                params = {
                    ...this.search,
                    ...this.page,
                    billState: '',
                }
                getExceptionList(params).then(res => {
                    const data = res.rows ? res.rows : [];
                    //处理数据序号
                    data.forEach((item, index) => {
                        item.index = (this.page.pageNum - 1) * this.page.pageSize + index + 1;
                        if (item.type == 4) { //国网账单 加特殊标识
                            item.index += ' 【国】';
                        }
                        if (item.subBills && item.subBills.length) {
                            item.subBills.forEach((p, k) => {
                                p.index = item.index + '.' + (k + 1);
                                p.isChild = true;
                            })
                        }
                    });
                    this.tableData = data;
                    this.total = res.total;
                })
            },
            showFp() { //发票预览
                let previewUrl = baseProfileUrl + this.detailData.stateGridPaper;
                if (!this.detailData.stateGridPaper) {
                    this.$message.warning('暂无图片')
                    return;
                }
                this.$viewerApi({
                    images: [previewUrl],
                })
            },
            //下载excel
            html2Excel() {
                const name = this.detailData.kwell ? this.detailData.kwell : this.detailData.kpName;
                new Table2Excel('#printArea table').export(
                    `${name}-${this.detailData.month}-${this.tableDialogType==1?'日用电明细表':'现场电量确认单'}`);
            },
            handleClose() {
                this.showTableDialog = false;
            },
            closeFlowDialog() {
                this.showTableFlowDialog = false;
            },
            //导出按钮
            downHand() {
                exportBill({
                    billState: '5',
                    ...this.search,
                }).then(tool.down)
            },

            btnClick(name, row) {
                this.detailData = row;
                this.haveFp = this.isYes(row.stateGirdPaperImp);
                if (name == "日用电明细") {
                    this.showTableDialog = true;
                    this.tableDialogType = 1; //日用电明细表
                    this.showPrintWd = true;
                } else if (name == "现场电量确认单") {
                    this.showTableDialog = true;
                    this.showPrintWd = true;
                    this.tableDialogType = 2; //现场电量确认单
                } else if (name == "流转") {
                    if (row.solveType < 6) {
                        this.$confirm('是否确认流转该账单?', '流转', {
                            confirmButtonText: '确定',
                            cancelButtonText: '取消',
                            type: 'warning'
                        }).then(() => {
                            //流转接口
                            postExceptionDo({
                                id: Number(row.id),
                                userId: Number(this.userId)
                            }).then(res => {
                                this.resPublick(res);
                            })
                        }).catch(() => {});
                    } else if (row.solveType == 6 || row.solveType == 7) {
                        this.selectBillDialog = true;
                        this.solveType = row.solveType;
                    }
                } else if (name == "编辑") { //根据异常状态处理异常
                    this.solveType = row.solveType;
                    this.exceptionDoDialog = true;
                }
            },
            //查看账单流程
            showBillFlow() {
                this.billId = this.detailData.id;
                this.showTableFlowDialog = true;
            },
            //异常处理账单
            exceptionHand(data) {
                if (!data) {
                    this.exceptionDoDialog = false;
                    return;
                }
                this.loading = true;
                //异常处理提交
                postExceptionSolve({
                    userId: this.userId,
                    ...data,
                    solveType: this.solveType,
                }).then(res => {
                    this.exceptionDoDialog = false;
                    this.resPublick(res);
                })
            },
            //选择结算单
            selectHand(data) {
                if (!data) {
                    this.selectBillDialog = false;
                    return;
                }
                this.$confirm('是否确认流转该账单?', '流转', {
                    confirmButtonText: '确定',
                    cancelButtonText: '取消',
                    type: 'warning'
                }).then(() => {
                    this.loading = true;
                    postExceptionDo({
                        id: Number(this.detailData.id),
                        ...data,
                        userId: Number(this.userId)
                    }).then(res => {
                        this.selectBillDialog = false;
                        this.resPublick(res);
                    })
                }).catch(() => {});
            },
            closed() {
                this.showPrintWd = false;
            },
            //数据维护
            dataInputHand(data) {
                if (!data) {
                    this.dataInputModelVisible = false;
                    return;
                }
                this.loading = true;
                postBillUpdate(data).then(res => {
                    this.dataInputModelVisible = false; //关闭弹窗
                    this.resPublick(res);
                })
            },
            //统一的接口返回提示接口
            resPublick(res) {
                this.loading = false; //停止加载
                this.$message({
                    type: 'success',
                    message: '操作成功'
                })
                this.reloadTable();
            },
        }
    }
</script>