<!--电量账单首页-->
<template>
    <el-card class="box-card">
        <el-tabs v-model="activeName" @tab-click="tabClick">
            <el-tab-pane v-if="isYwRole" label="账单生成" name="0">
                <BillCreate ref="0"></BillCreate>
            </el-tab-pane>
            <el-tab-pane v-if="isYwRole" label="用户确认" name="1">
                <UserComfirm ref="1"></UserComfirm>
            </el-tab-pane>
            <el-tab-pane v-if="isMarketRole" label="市场/客户确认" name="2">
                <MarketComfirm ref="2"></MarketComfirm>
            </el-tab-pane>
            <el-tab-pane v-if="isMarketRole" label="闭环处理" name="3">
                <CompleteBill ref="3"></CompleteBill>
            </el-tab-pane>
            <el-tab-pane label="异常处理" name="4">
                <ExceptionList ref="4"></ExceptionList>
            </el-tab-pane>
            <el-tab-pane label="账单查询" name="5">
                <BillSearch ref="5"></BillSearch>
            </el-tab-pane>
        </el-tabs>
    </el-card>
</template>

<script>
    import BillCreate from "./BillCreate.vue";
    import UserComfirm from "./UserComfirm.vue";
    import MarketComfirm from "./MarketComfirm.vue";
    import CompleteBill from "./CompleteBill.vue";
    import BillSearch from "./BillSearch.vue";
    import ExceptionList from "./ExceptionList.vue";
    import {
        ROLENAME
    } from "@/utils/config.js";
    import {
        mapState
    } from 'vuex';
    export default {
        name: 'index',
        components: {
            BillCreate, //账单生成
            UserComfirm, //用户确认
            MarketComfirm, //用户确认
            CompleteBill, //闭环
            BillSearch, //账单查询
            ExceptionList, //账单查询
        },
        data() {
            return {
                activeName: '0',
            }
        },
        computed: {
            ...mapState({ //角色权限
                isMarketRole: state => state.permission.roles.includes(ROLENAME.marketer),
                isYwRole: state => state.permission.roles.includes(ROLENAME.yw),
            }),
        },
        mounted() {
            this.activeName = this.isYwRole ? '0' : '2';
        },
        methods: {
            tabClick({
                name
            }) {
                this.$refs[name] && this.$refs[name].reloadTable();
            }
        }
    }
</script>

<style scoped lang="scss">
    .box-card {
        width: 100%;

        /deep/ .el-tabs__content {
            width: 100%;
        }

        /deep/ .el-card__body {
            padding: 0 0.24rem 0.24rem 0.24rem;
        }
    }
</style>