                <div class="boxs">
                    <el-upload :class="{hide:pathDlmx.length}" :action="fileUploadUrl" :headers="headers"
                        :on-success="handleChangeTwo" :before-upload="beforeUploadPublic" list-type="picture-card"
                        :limit="1" :file-list="pathDlmx">
                        <i slot="default" class="el-icon-plus"></i>
                        <div slot="file" slot-scope="{file}">
                            <img v-if="isImg(file)" class="el-upload-list__item-thumbnail" :src="file.url" alt="">
                            <div v-else class="box">
                                <svg-icon :icon-class="getPreType(file)" class-name="logo-img"></svg-icon>
                            </div>
                            <span class="el-upload-list__item-actions">
                                <span v-if="isImg(file)" class="el-upload-list__item-preview"
                                    @click="handlePictureCardPreview(file)">
                                    <i class="el-icon-zoom-in"></i>
                                </span>
                                <span class="el-upload-list__item-delete" @click="handleRemove(pathDlmx,file)">
                                    <i class="el-icon-delete"></i>
                                </span>
                            </span>
                        </div>
                    </el-upload>
                </div>
            </div>
        </div>
        <div class="item">
            <div class="title"><span class="important">*</span>内部电量确认单</div>
            <div class="files">
                <div class="label">附件上传：</div>
                <div class="boxs">
                    <el-upload :class="{hide:pathDlnqr.length}" :action="fileUploadUrl" :headers="headers"
                        :on-success="handleChangeThree" :before-upload="beforeUploadPublic" list-type="picture-card"
                        :limit="1" :file-list="pathDlnqr">
                        <i slot="default" class="el-icon-plus"></i>
                        <div slot="file" slot-scope="{file}">
                            <img v-if="isImg(file)" class="el-upload-list__item-thumbnail" :src="file.url" alt="">
                            <div v-else class="box">
                                <svg-icon :icon-class="getPreType(file)" class-name="logo-img"></svg-icon>
                            </div>
                            <span class="el-upload-list__item-actions">
                                <span v-if="isImg(file)" class="el-upload-list__item-preview"
                                    @click="handlePictureCardPreview(file)">
                                    <i class="el-icon-zoom-in"></i>
                                </span>
                                <span class="el-upload-list__item-delete" @click="handleRemove(pathDlnqr,file)">
                                    <i class="el-icon-delete"></i>
                                </span>
                            </span>
                        </div>
                    </el-upload>
                </div>
            </div>
        </div>

        <div class="item">
            <div class="title"><span class="important">*</span>外部电量确认单</div>
            <div class="files">
                <div class="label">附件上传：</div>
                <div class="boxs">
                    <el-upload :class="{hide:pathDfqrd.length}" :action="fileUploadUrl" :headers="headers"
                        :on-success="handleChangeFour" :before-upload="beforeUploadPublic" list-type="picture-card"
                        :limit="1" :file-list="pathDfqrd">
                        <i slot="default" class="el-icon-plus"></i>
                        <div slot="file" slot-scope="{file}">
                            <img v-if="isImg(file)" class="el-upload-list__item-thumbnail" :src="file.url" alt="">
                            <div v-else class="box">
                                <svg-icon :icon-class="getPreType(file)" class-name="logo-img"></svg-icon>
                            </div>
                            <span class="el-upload-list__item-actions">
                                <span v-if="isImg(file)" class="el-upload-list__item-preview"
                                    @click="handlePictureCardPreview(file)">
                                    <i class="el-icon-zoom-in"></i>
                                </span>
                                <span class="el-upload-list__item-delete" @click="handleRemove(pathDfqrd,file)">
                                    <i class="el-icon-delete"></i>
                                </span>
                            </span>
                        </div>
                    </el-upload>
                </div>
            </div>
        </div>
        <div class="item">
            <div class="title">其他</div>
            <div class="files">
                <div class="label">附件上传：</div>
                <div class="boxs">
                    <el-upload :class="{hide:pathOther.length}" :action="fileUploadUrl" :headers="headers"
                        :on-success="handleChangeFive" :before-upload="beforeUploadPublic" list-type="picture-card"
                        :limit="1" :file-list="pathOther">
                        <i slot="default" class="el-icon-plus"></i>
                        <div slot="file" slot-scope="{file}">
                            <img v-if="isImg(file)" class="el-upload-list__item-thumbnail" :src="file.url" alt="">
                            <div v-else class="box">
                                <svg-icon :icon-class="getPreType(file)" class-name="logo-img"></svg-icon>
                            </div>
                            <span class="el-upload-list__item-actions">
                                <span v-if="isImg(file)" class="el-upload-list__item-preview"
                                    @click="handlePictureCardPreview(file)">
                                    <i class="el-icon-zoom-in"></i>
                                </span>
                                <span class="el-upload-list__item-delete" @click="handleRemove(pathOther,file)">
                                    <i class="el-icon-delete"></i>
                                </span>
                            </span>
                        </div>
                    </el-upload>
                </div>
            </div>
        </div>
        <div class="item allLine noBottom">
            <UploadProgress ref="uploadPreogress" />
        </div>
    </div>
</template>
<script>
    import {
        fileUploadUrl
    } from '@/api/archives';
    import {
        getToken
    } from '@/utils/auth'
    import {
        Message
    } from 'element-ui';
    import {
        baseUrl,
        baseProfileUrl
    } from '@/api/baseUrl.js';
    import tool from '@/utils/tool'
    import UploadProgress from "@/components/UploadProgress.vue"; //进度条
    export default {
        components: {
            UploadProgress,
        },
        props: {
            billId: Number,
        },
        data() {
            return {
                fileUploadUrl, //上传文件地址
                headers: {
                    uapAccessToken: getToken('accessToken'),
                    uapRefreshToken: getToken('refreshToken'),
                },
                pathDlqr: [], //现场电量确认单
                pathDlmx: [], //日用电明细表
                pathDlnqr: [], // 内部电量确认单
                pathDfqrd: [], // 外部电量电费签认单
                pathOther: [], // 其他
            }
        },
        mounted() {
            this.$refs.uploadPreogress && this.$refs.uploadPreogress.init();
        },
        methods: {
            isImg(file) {
                const name = file.name.toLowerCase();
                return ['.png', '.jpeg', '.jpg'].find(item => {
                    return name.indexOf(item) !== -1;
                })
            },
            //获取svg文件名
            getPreType(file) {
                const name = file.name.toLowerCase();
                const baseImg = ''
                if (name.indexOf('.doc') !== -1 || name.indexOf('.docx') !== -1) { // word
                    return 'w'
                } else if (name.indexOf('.pdf') !== -1) {
                    return 'pdf'
                } else if (name.indexOf('.ppt') !== -1 || name.indexOf('.pptx') !== -1) {
                    return 'ppt'
                } else if (name.indexOf('.xls') !== -1 || name.indexOf('.xlsx') !== -1) {
                    return 's'
                }
                return 'zip';
            },
            handleAvatarSuccess(res, file) {
                if (res.code !== 200) {
                    Message.error({
                        message: '上传失败' + res.msg,
                    })
                    return;
                }
            },
            //删除图片
            handleRemove(data, file) {
                let index = 0;
                data.map((item, index) => {
                    if (item.name === file.name) {
                        index = index;
                    }
                })
                data.splice(index, 1);
                this.$refs.uploadPreogress.success();
            },
            //预览图片
            handlePictureCardPreview(file) {
                this.$viewerApi({
                    images: [file.url],
                })
            },
            //现场电量确认单
            handleChangeOne(res, file, fileList) {
                if (res.code == 200) {
                    this.pathDlqr = fileList.slice(0);
                    this.$refs.uploadPreogress.success();
                } else {
                    this.msg(res.msg);
                    this.pathDlqr = [];
                }
            },
            //日用电明细
            handleChangeTwo(res, file, fileList) {
                if (res.code == 200) {
                    this.pathDlmx = fileList.slice(0);
                    this.$refs.uploadPreogress.success();
                } else {
                    this.msg(res.msg);
                    this.pathDlmx = [];
                }
            },
            //内部电量确认单
            handleChangeThree(res, file, fileList) {
                if (res.code == 200) {
                    this.pathDlnqr = fileList.slice(0);
                    this.$refs.uploadPreogress.success();
                } else {
                    this.msg(res.msg);
                    this.pathDlnqr = [];
                }
            },
            //外部电量电费签认单
            handleChangeFour(res, file, fileList) {
                if (res.code == 200) {
                    this.pathDfqrd = fileList.slice(0);
                    this.$refs.uploadPreogress.success();
                } else {
                    this.msg(res.msg);
                    this.pathDfqrd = [];
                }
            },
            //其他
            handleChangeFive(res, file, fileList) {
                if (res.code == 200) {
                    this.pathOther = fileList.slice(0);
                    this.$refs.uploadPreogress.success();
                } else {
                    this.msg(res.msg);
                    this.pathOther = [];
                }
            },
            //上传验证是否已经正在上传,如果是图片压缩
            beforeUploadPublic(file) {
                if (this.$refs.uploadPreogress.isLoading()) return false;
                this.$refs.uploadPreogress.start(); //开始增加进度
                const name = file.name.toLowerCase();
                const isPic = ['.png', '.jpeg', '.jpg'].find(item => {
                    return name.indexOf(item) !== -1;
                })
                const isLt2M = file.size / 1024 / 1024 < 2;
                if (!isPic || isLt2M) {
                    return true;
                }
                return tool.compressImg(file);
            },
            //提交
            submitForm(success) {
                if (this.$refs.uploadPreogress.isLoading()) { //正在上传文件
                    this.$message.warning('正在上传文件，请稍后再试');
                    return;
                }
                if (!this.pathDlqr[0]) {
                    this.$message.info('请上传现场电量确认单');
                    return;
                }
                if (!this.pathDlnqr[0]) {
                    this.$message.info('请上传内部电量确认单');
                    return;
                }
                if (!this.pathDfqrd[0]) {
                    this.$message.info('请上传外部电量确认单');
                    return;
                }

                const data = {
                    pathDlqr: this._getUrl(this.pathDlqr[0]),
                    pathDlmx: this._getUrl(this.pathDlmx[0]),
                    pathDlnqr: this._getUrl(this.pathDlnqr[0]),
                    pathDfqrd: this._getUrl(this.pathDfqrd[0]),
                    pathOther: this._getUrl(this.pathOther[0]),
                    id: this.billId,
                }
                success(data);
            },
            //根据文件名获取文件名称
            _getFileNameByUrl(url) {
                if (!url) return '';
                const _index = url.indexOf('_');
                return _index !== -1 ? url.slice(_index + 1) : url;
            },
            //获取保存的url地址
            _getUrl(item) {
                if (!item) return '';
                if (item.response) {
                    return item.response.fileName;
                } else if (item.url) {
                    return item.url.replace(baseProfileUrl, '');
                }
                return '';
            },
            //获取完整绝对文件地址
            getShowUrl(url) {
                if (!url) return '';
                return baseProfileUrl + url;
            },
            msg(err) {
                this.$message.error('上传出错:' + err);
            },
            reset() {
                this.pathDlqr = [];
                this.pathDlmx = [];
                this.pathDlnqr = [];
                this.pathDfqrd = [];
                this.pathOther = [];
                this.$refs.uploadPreogress && this.$refs.uploadPreogress.init();
            }
        },
        watch: {
            billId: {
                handler(val) {
                    //复盘文件
                    if (!val) return;
                    this.reset();
                },
            }
        }
    }
</script>

