<!--闭环-->
<template>
    <div>
        <SearchInputs @downHand="downHand" @searchHand="searchHand"></SearchInputs>
        <CusTable :tableData="tableData" @btnClick="btnClick" @handleSizeChange="handleSizeChange"
            @handleCurrentChange="handleCurrentChange" :pageNum="page.pageNum" :pageSize="page.pageSize" :total="total">
        </CusTable>
        <!--弹框 现场电量确认单-->
        <el-dialog custom-class="cusDialog" :visible.sync="showTableDialog" width="10rem" :before-close="handleClose"
            @closed="closed">
            <div class="cus-title" slot="title">
                <div class="title-btn" :class="{active: tab == 1}" @click="titleTab(1)">内部确认单</div>
                <div class="title-btn" :class="{active: tab == 2}" @click="titleTab(2)">外部电量电费确认单{{moreTab?'(1)':''}}
                </div>
                <div v-show="moreTab" class="title-btn" :class="{active: tab == 3}" @click="titleTab(3)">外部电量电费确认单(2)
                </div>
            </div>
            <div class="print-body">
                <!--弹框  打印-->
                <div key="1" v-if="showPrintWd&&tab==1">
                    <TempalteBill :type="2" :tableData="detailData"></TempalteBill>
                </div>
                <div key="2" v-if="showPrintWd&&tab==2">
                    <TempalteBill2 :type="3" :tableData="detailData"></TempalteBill2>
                </div>
                <div key="3" v-if="showPrintWd&&tab==3">
                    <TempalteBill3 :type="5" :tableData="detailData"></TempalteBill3>
                </div>
            </div>

            <span slot="footer" class="dialog-footer">
                <el-button type="primary" @click="html2Excel">下 载</el-button>
                <el-button type="primary" @click="printDo">打 印</el-button>
                <el-button @click="handleClose">取 消</el-button>
                <span id="print-btn" v-print="printObj"></span>
            </span>
        </el-dialog>
        <!--文件上传闭环-->
        <el-dialog title="流程闭环" custom-class="cusDialog" :visible.sync="complelteModelVisible" width="7rem"
            :before-close="handleCloseUpload">
            <Upload ref="upload" :billId="billId"></Upload>
            <span slot="footer" class="dialog-footer">
                <el-button type="primary" :loading="loading" @click="submit">提 交</el-button>
                <el-button @click="handleCloseUpload">关 闭</el-button>
            </span>
        </el-dialog>
        <!--账单查看和维护-->
        <el-dialog title="账单查看和维护" custom-class="cusDialog" :visible.sync="editModelVisible" width="7rem"
            :before-close="handleCloseEdit">
            <EditBills ref="EditBills" :billId="billId" @reloadTable="reloadTable"></EditBills>
            <span slot="footer" class="dialog-footer">
                <el-button @click="handleCloseEdit">关 闭</el-button>
            </span>
        </el-dialog>
    </div>
</template>
<script>
    //组件
    import TableCommon from "@/components/TableCommon.vue";
    import CusTable from "./components/completeBill/BillTable.vue";
    import SearchInputs from "../components/SearchInputsCollec.vue"
    import ErrorDlModel from "./components/common/ErrorDlModel.vue";
    import Upload from "./components/completeBill/Upload.vue";
    import EditBills from "./components/completeBill/EditBills.vue";
    import TempalteBill from "@/components/tempalteBill.vue";
    import TempalteBill2 from "@/components/tempalteBill.vue";
    import TempalteBill3 from "@/components/tempalteBill.vue";
    import {
        parseTime
    } from "@/utils/index";
    //接口
    import {
        getClosedBill, //账单生成列表
        postDoneBill, //上传文件闭环
        exportFinalBill, //结算单数据导出
        getBillInfo, //打印
    } from "@/api/archives.js";
    //公用函数
    import tool from "@/utils/tool";
    import Table2Excel from 'table2excel.js'
    import {
        mapState
    } from 'vuex';
    export default {
        extends: TableCommon,
        name: 'CompleteBill',
        components: {
            CusTable,
            SearchInputs,
            Upload, //上传文件
            TempalteBill, //打印
            TempalteBill2, //打印
            TempalteBill3, //打印
            EditBills, //维护
        },
        data() {
            return {
                //表单
                complelteModelVisible: false, //上传文件闭环弹框
                editModelVisible: false, //上传文件闭环弹框
                showTableDialog: false, //现场电量确认单查看弹框
                detailData: {}, //单条账单详细数据
                billId: '', //账单id
                showPrintWd: false, //控制打印组件销毁和显示
                tab: 1,
                moreTab: false, //是否显示第三个tab 用于四川页岩气公司打印外部确认单（2张）
                printObj: { //打印参数
                    id: "printArea",
                    popTitle: '　',
                }
            }
        },
        computed: {
            ...mapState({
                userId: state => state.user.userId, //当前登录的user
            })
        },
        mounted() {},
        methods: {
            parseTime,
            _getData(params = {}) { // 通过接口 获取最新数据 如果无接口 则是第一次加载
                params = {
                    ...this.search,
                    ...this.page,
                    status: 'A', //未闭环
                }
                getClosedBill(params).then(res => {
                    const data = res.rows ? res.rows : [];
                    this.tableData = data;
                    this.total = res.total;
                })
            },
            titleTab(tab) {
                this.tab = tab;
            },
            //下载excel
            html2Excel() {
                const name = this.detailData.company;
                const nameArr = ['', '内部确认单', '外部电量电费确认单', '外部电量电费确认单2'];
                const date = this.detailData.billDate.replace('年', '-').replace('月', '');
                new Table2Excel('#printArea table').export(`${name}-${date}-${nameArr[this.tab]}`);
            },
            //导出按钮
            downHand() {
                exportFinalBill({
                    ...this.search,
                    status: 'A', //未闭环
                }).then(tool.down)
            },
            handleClose() {
                this.showTableDialog = false;
            },
            handleCloseUpload() {
                this.complelteModelVisible = false;
            },
            handleCloseEdit() {
                this.editModelVisible = false;
            },
            //打印点击 提示计量点是否全部确认
            printDo() {
                getBillInfo({
                    id: this.billId
                }).then(res => {
                    const msg = res.msg;
                    this.$confirm(msg.replace(/\n/g, ' ').replace("：", '：<br/>  '), '', {
                        confirmButtonText: '确认打印',
                        cancelButtonText: '取消',
                        type: ~msg.indexOf('都已确认') ? 'success' : 'warning',
                        dangerouslyUseHTMLString: true
                    }).then(() => {
                        document.getElementById('print-btn').click();
                    })
                })
            },
            btnClick(name, row) {
                this.detailData = row;
                this.billId = row.id;
                if (name == "查看") {
                    this.showTableDialog = true;
                    this.showPrintWd = true;
                    //判断是否是四川页岩气 是的话显示2张外部确认单
                    this.moreTab = row.company.indexOf('四川页岩气') !== -1;
                } else if (name == "闭环") {
                    this.complelteModelVisible = true;
                } else if (name == "账单维护") {
                    this.editModelVisible = true;
                }
            },
            submit() {
                this.$refs['upload'].submitForm((data) => {
                    this.loading = true; //加载
                    postDoneBill({
                        userId: this.userId,
                        ...data
                    }).then(res => {
                        this.complelteModelVisible = false; //关闭弹框
                        this.resPublick(res);
                    })
                })
            },
            //打印
            closed() {
                this.showPrintWd = false;
            },
            //统一的接口返回提示接口
            resPublick(res) {
                this.loading = false; //停止加载
                this.$message({
                    type: 'success',
                    message: '操作成功'
                })
                this.reloadTable();
            }
        }
    }
</script>

<style scoped lang="scss">
    .cus-title {
        display: flex;
        user-select: none;

        .title-btn {
            &.active {
                color: #2E4AF7;
            }

            cursor: pointer;
            margin-right: 60px;
            font-weight: 500;
            color: #333333;
            line-height: 20px;
            font-family: "PFC"
        }
    }
</style>