<template>
    <!--异常处理-->
    <el-dialog :title="title" custom-class="cusDialog" :visible.sync="show" width="5rem" :before-close="handleClose">
        <el-form class="form" :model="selectedObj" :rules="rules" ref="form">
            <div class="li">
                <div class="item">
                    <el-form-item>
                        <div class="label">平台名称：</div>
                        <div class='rt'>
                            <div class="box">{{selectedObj.kpName}}</div>
                        </div>
                    </el-form-item>
                </div>
            </div>
            <div class="li">
                <div class="item">
                    <el-form-item>
                        <div class="label">用电单位：</div>
                        <div class='rt'>
                            <div class="box">{{selectedObj.sdName}}</div>
                        </div>
                    </el-form-item>
                </div>
            </div>
            <div class="li">
                <div class="item">
                    <div class="label">结算月份：</div>
                    <div class='rt'>
                        <div class="box">{{ changeMonth(selectedObj.month)}}</div>
                    </div>
                </div>
            </div>
            <div class="li">
                <div class="item">
                    <div class="label">结算时间：</div>
                    <div class='rt'>
                        <div class="box">
                            {{ parseTime(selectedObj.arSt,'{y}/{m}/{d} {h}:{i}') + "-"+ parseTime(selectedObj.arEt,'{y}/{m}/{d} {h}:{i}')}}
                        </div>
                    </div>
                </div>
            </div>
            <!--修改起止码-->
            <div class="li" v-if="type==1||type==4">
                <div class="item">
                    <el-form-item prop="arSnum">
                        <div class="label"><span class="important">*</span>起码：</div>
                        <el-input type="number" :min="0" :max="selectedObj.arEnum" v-model="selectedObj.arSnum"
                            placeholder="请输入起码"></el-input>
                    </el-form-item>
                </div>
            </div>

            <div class="li" v-if="type==1||type==4">
                <div class="item">
                    <el-form-item prop="arEnum">
                        <div class="label"><span class="important">*</span>止码：</div>
                        <el-input type="number" @blur="inputE" :min="selectedObj.arSnum" v-model="selectedObj.arEnum"
                            placeholder="请输入止码"></el-input>
                    </el-form-item>
                </div>
            </div>
            <div class="li" v-if="type==1||type==4">
                <div class="item">
                    <div class="label">倍率：</div>
                    <div class='rt'>
                        <div class="box">{{selectedObj.blRate}}</div>
                    </div>
                </div>
            </div>

            <!--修改起止码并上传日明细-->
            <div class="li" v-if="type==2">
                <div class="item">
                    <el-form-item prop="arSnum">
                        <div class="label"><span class="important">*</span>日用明细表：</div>
                        <el-upload class="upload-demo" :action="fileUploadUrl" :headers="headers"
                            :on-success="handleChange" :before-upload="beforeUpload" :on-error="error" :data="params"
                            :show-file-list="false">
                            <a :loading="upLoading" class="fileName"
                                :title="excelName">{{fileList.length?excelName:'点击上传'}}
                            </a>
                        </el-upload>
                    </el-form-item>
                </div>
            </div>
            <div class="li" v-if="type==2">
                <div class="item">
                    <el-form-item>
                        <div class="label">日用表总电量：</div>
                        <span v-if="fileList.length" class="unit">{{selectedObj.drEq}} kW·h <small
                                class="notice">表底差值:{{calc}}</small></span>
                        <span v-else class="unit red">成功导入【日用明细表】后可见</span>
                    </el-form-item>
                </div>
            </div>
            <div class="li" v-if="type==2">
                <div class="item">
                    <el-form-item prop="arSnum">
                        <div class="label"><span class="important">*</span>起码：</div>
                        <el-input type="number" :min="0" @input="inputS" v-model="selectedObj.arSnum"
                            placeholder="请输入起度">
                        </el-input>
                    </el-form-item>
                </div>
            </div>
            <div class="li" v-if="type==2">
                <div class="item">
                    <el-form-item prop="arEnum">
                        <div class="label"><span class="important">*</span>止码：</div>
                        <el-input type="number" @blur="inputE" :min="selectedObj.arSnum" v-model="selectedObj.arEnum"
                            placeholder="请输入止度"></el-input>
                    </el-form-item>
                </div>
            </div>
            <div class="li" v-if="type==2">
                <div class="item">
                    <div class="label">倍率：</div>
                    <div class='rt'>
                        <div class="box">{{selectedObj.blRate}}</div>
                    </div>
                </div>
            </div>


            <div class="li" v-if="type==3||type==4">
                <div class="item">
                    <div class="label"><span class="important">*</span>模拟发票：</div>
                    <div class='rt'>
                        <el-upload :class="{hide:filesFP.length,'FPfile':true}" :action="fileUploadUrlFP"
                            :headers="headers" :on-success="handleChangeFP" :before-upload="beforeUploadFP"
                            list-type="picture-card" :limit="2" :file-list="filesFP">
                            <i slot="default" class="el-icon-plus"></i>
                            <div slot="file" slot-scope="{file}">
                                <img v-if="isImg(file)" class="el-upload-list__item-thumbnail" :src="file.url" alt="">
                                <div v-else class="box">
                                    <svg-icon :icon-class="getPreType(file)" class-name="logo-img">
                                    </svg-icon>
                                </div>
                                <span class="el-upload-list__item-actions">
                                    <span v-if="isImg(file)" class="el-upload-list__item-preview"
                                        @click="handlePictureCardPreview(file)">
                                        <i class="el-icon-zoom-in"></i>
                                    </span>
                                    <span class="el-upload-list__item-delete" @click="handleRemove(filesFP,file)">
                                        <i class="el-icon-delete"></i>
                                    </span>
                                </span>
                            </div>
                        </el-upload>
                        <UploadProgress ref="uploadPreogress" />
                    </div>
                </div>
            </div>

            <!--通用的除了市场异常处理-->
            <div class="li" v-if="type!=6&&type!=7">
                <div class="item">
                    <el-form-item>
                        <div class="label">损耗分摊：</div>
                        <el-input v-model="selectedObj.loseEq" type="number" min="0" placeholder="请输入电量">
                            <template slot="append">kW·h</template>
                        </el-input>
                    </el-form-item>
                </div>
            </div>

            <div class="li" v-if="type==1||type==2||type==4||type==6||type==7">
                <div class="item">
                    <div class="label">总电量：</div>
                    <div class='rt'>
                        <div class="box">{{calcAllEq}}</div>
                    </div>
                </div>
            </div>
            <!--上传模拟发票-->
            <div class="li" v-if="type==3">
                <div class="item">
                    <el-form-item prop="eqValue">
                        <div class="label"><span class="important">*</span>电量：</div>
                        <el-input v-model="selectedObj.eqValue" type="number" min="0" placeholder="请输入电量">
                            <template slot="append">kW·h</template>
                        </el-input>
                    </el-form-item>
                </div>
            </div>
            <div class="li" v-if="type==3">
                <div class="item">
                    <div class="label">总电量：</div>
                    <div class='rt'>
                        <div class="box">{{allEqFP}}</div>
                    </div>
                </div>
            </div>
            <!--重算-->

            <div class="li" v-if="type==6|| type==7">
                <div class="item">
                    <div class="label">合同信息：</div>
                    <div class='rt'>
                        <div class="box contract">
                            <template v-if="contract.name">
                                <div class="li">
                                    <div class="lt" style="width:100%">
                                        <span>{{contract.name}}</span>
                                    </div>
                                </div>
                                <div class="li">
                                    <div class="lt">
                                        <label>单价：</label> <span>{{contract.price}} 元/度</span>
                                    </div>
                                    <div class="rt">
                                        <label>用电性质：</label> <span>{{contract.wkTy}}</span>
                                    </div>
                                </div>
                                <div class="li">
                                    <div class="lt">
                                        <label>运营类型：</label> <span>{{contract.devType}}</span>
                                    </div>
                                    <div class="rt">
                                        <label>税率：</label> <span>{{contract.taxRate*100}}%</span>
                                    </div>
                                </div>
                            </template>
                            <div class="li" v-else>
                                <div class="lt" style="width:100%">
                                    <span>无关联合同，请联系相关人员关联后再试。</span>
                                </div>
                            </div>
                        </div>
                    </div>
                </div>
            </div>
            <div class="li" v-if="(type==6 || type==7) && contract.name">
                <div class="item">
                    <el-form-item prop="tempPrice">
                        <div class="label">临时单价：</div>
                        <el-input v-model="selectedObj.tempPrice" type="number" min="0" placeholder="请输入单价">
                            <template slot="append">元/度</template>
                        </el-input>
                    </el-form-item>
                </div>
            </div>
            <div class="li" v-if="type==6|| type==7">
                <div class="item">
                    <div class="label">结算电费：</div>
                    <div class='rt'>
                        <div class="box">{{totalPrice}}
                            <span class="unit">元</span>
                        </div>
                    </div>
                </div>
            </div>
        </el-form>
        <span slot="footer" class="dialog-footer">
            <el-button type="primary" :disabled="canSubmit" :loading="loading" @click="submit">提 交
            </el-button>
            <el-button @click="handleClose">关 闭</el-button>
        </span>
    </el-dialog>
</template>
<script>
    import {
        Message
    } from 'element-ui';
    import {
        parseTime,
        formatNum
    } from "@/utils/index";
    import tool from '@/utils/tool'
    import {
        fileUploadUrl,
        resourceUrl,
        billExport,
        getExcepContractQuery
    } from '@/api/archives';
    import {
        baseProfileUrl
    } from '@/api/baseUrl.js';
    import {
        getToken
    } from '@/utils/auth'
    import UploadProgress from "@/components/UploadProgress.vue"; //进度条
    let initObj = {
        id: "", //唯一id
        kpName: '', //平台名称
        sdName: '', //用电单位
        month: '', //结算月份
        arSnum: 0, //起码
        arEnum: 0, //止码
        blRate: 0, //倍率
        allEq: 0, //总电量
        loseEq: 0, // 分摊
        drEq: 0, //导入明细表的电量
        eqValue: 0, //上传模拟发票中间变了
        tempPrice: '', //临时单价
        estimatePrice: '', //当前的临时单价
    };
    export default {
        components: {
            UploadProgress,
        },
        props: {
            show: { //显隐
                type: Boolean,
                default: false,
            },
            type: { //异常类型
                type: String,
                default: 1,
            },
            detailData: { //账单数据
                type: Object,
                default: () => {},
            },
            loading: Boolean
        },
        data() {
            return {

                fileList: [], //日明细文件
                filesFP: [], //发票文件
                upLoading: false, //上传状态
                excelName: '', //excel名称
                headers: {
                    uapAccessToken: getToken('accessToken'),
                    uapRefreshToken: getToken('refreshToken'),
                },
                contract: {}, //合同信息
                selectedObj: {
                    ...initObj,
                },
                rules: {
                    blRate: [{
                        validator: (rule, value, callback) => {
                            if (value === '') {
                                callback(new Error('请输入倍率'));
                            } else if (value <= 0) {
                                callback(new Error('倍率需大于0'));
                            } else {
                                callback()
                            }
                        },
                    }],
                    arSnum: [{ //起码
                        validator: (rule, value, callback) => {
                            if (value === '' || isNaN(value)) {
                                callback(new Error('请输入起码'));
                            } else if (value < 0) {
                                callback(new Error('起码需大于等于0'));
                            } else if (Number(value) > Number(this.selectedObj.arEnum)) {
                                callback(new Error('起码需小于等于止码'))
                            } else {
                                // this.$refs['form'].validateField('arEnum');
                                callback()
                            }
                        },
                        trigger: 'change'
                    }],
                    arEnum: [{ //止码
                        validator: (rule, value, callback) => {
                            if (value === '' || isNaN(value)) {
                                callback(new Error('请输入止码'));
                            } else if (value < 0) {
                                callback(new Error('止码需大于等于0'));
                            } else if (Number(value) < Number(this.selectedObj.arSnum)) {
                                callback(new Error('止码需大于等于起码'))
                            } else {
                                this.$refs['form'].validateField('arSnum');
                                callback()
                            }
                        },
                        trigger: 'change'
                    }],
                    tempPrice: [{ //单价
                        validator: (rule, value, callback) => {
                            if (value == '' || value == undefined) return callback();
                            if (isNaN(value) || value != Number(value)) {
                                callback(new Error('请输入正确的单价'));
                            } else if (value <= 0) {
                                callback(new Error('单价需大于等于0'));
                            } else {
                                callback()
                            }
                        },
                        trigger: 'change'
                    }],
                    eqValue: [{ //电量
                        validator: (rule, value, callback) => {
                            if (value == '') {
                                callback(new Error('请输入电量'));
                            } else if (value < 0) {
                                callback(new Error('电量需大于等于0'));
                            } else {
                                callback()
                            }
                        },
                        trigger: 'change'
                    }],
                },
                textArr: ['', '修改起止码', '修改起止码并上传日明细', '上传模拟发票', '修改起止码并上传模拟发票', '', '合同单价维护', '合同单价维护'],
            }
        },
        mounted() {
            if (!this.detailData) return;
            this.initPage(this.detailData);
        },
        computed: {
            //是否可提交价格修改
            canSubmit() {
                if (this.type <= 4) return false;
                return !(this.contract.name && this.selectedObj.tempPrice)
            },
            fileUploadUrl() {
                return billExport(this.selectedObj.id);
            },
            fileUploadUrlFP() {
                return fileUploadUrl;
            },
            title() {
                return '异常处理-' + this.textArr[this.type];
            },
            //止-起 倍率算出来的
            useEq() {
                if (this.selectedObj.arEnum < this.selectedObj.arSnum) {
                    return '-';
                }
                const sum = ((this.selectedObj.arEnum - this.selectedObj.arSnum).toFixed(6) * this.selectedObj.blRate)
                    .toFixed(2);
                return formatNum(sum)
            },
            //总电量=（起度-止度）*倍率 + 损耗电量
            calcAllEq() {
                const sum = ((this.selectedObj.arEnum - this.selectedObj.arSnum).toFixed(6) * this.selectedObj.blRate)
                    .toFixed(2);
                let calc = formatNum(sum) < 0 ? 0 : formatNum(sum);
                return formatNum(calc + Number(this.selectedObj.loseEq))
            },

            allEqFP() { //模拟发票总电量
                return formatNum(Number(this.selectedObj.eqValue) + Number(this.selectedObj.loseEq));
            },
            calc() { //到入日明细后 底码差值
                return Number((this.selectedObj.drEq / this.selectedObj.blRate).toFixed(5));
            },
            totalPrice() {
                let price = (this.selectedObj.tempPrice && this.selectedObj.tempPrice != 0) ? this.selectedObj
                    .tempPrice : this.contract.price
                return price ? formatNum(price * this.calcAllEq) : '-';
            },
            params() { //上传问价参数
                return {
                    id: this.selectedObj.id,
                }
            },
        },
        methods: {
            parseTime,
            initPage(val) { //初始化参数
                this.selectedObj = {
                    ...initObj
                };
                for (let key in val) {
                    this.selectedObj[key] = val[key];
                }
                if (val.stateGridPaper) {
                    this.filesFP = [{
                        url: this.getShowUrl(val.stateGridPaper),
                        name: this._getFileNameByUrl(val.stateGridPaper),
                    }];
                }
                //日明细文件名
                if (val.dayPaperFile) {
                    this.fileList = [{
                        url: this.getShowUrl(val.dayPaperFile),
                        name: this._getFileNameByUrl(val.dayPaperFile),
                    }];
                    this.excelName = this._getFileNameByUrl(val.dayPaperFile); //保存上传的excel名称
                    this.selectedObj.drEq = this.selectedObj.allEq - this.selectedObj.loseEq;
                } else {
                    this.fileList = [];
                    this.excelName = '';
                    this.selectedObj.drEq = 0;
                }
                if (this.type == 3) { //上传模拟发票
                    this.selectedObj.eqValue = this.selectedObj.allEq - this.selectedObj.loseEq;
                }
                if (this.type == 6 || this.type == 7) { //价格个异常
                    this.getContractById(val.id)
                }
            },
            // 根据账单id获取关联合同
            getContractById(billId) {
                getExcepContractQuery({
                    billId,
                }).then(res => {
                    this.contract = res.data ? res.data : {};
                    // 有合同且临时价格和合同价格不同时 显示默认的临时价格
                    if (res.data && this.selectedObj.estimatePrice && this.selectedObj.estimatePrice != this
                        .contract
                        .price) {
                        this.selectedObj.tempPrice = this.selectedObj.estimatePrice;
                    } else {
                        this.selectedObj.tempPrice = '';
                    }
                })
            },
            changeMonth(t) {
                return parseTime(new Date(t), '{y}年{m}月')
            },
            handleClose() {
                this.$emit('exceptionHand')
            },
            //异常处理提交表单
            submit() {
                let opt = {};
                this.$refs['form'].validate((valid) => {
                    if (valid) {
                        if (this.allEqFP < 0 || this.calcAllEq < 0) {
                            this.$message({
                                type: 'warning',
                                message: '电量不能小于0',
                            })
                            return;
                        }
                        if (this.type == 1) { //修改起止码
                            opt = {
                                id: this.selectedObj.id,
                                arEnum: this.selectedObj.arEnum * 1, //止码
                                arSnum: this.selectedObj.arSnum * 1, //止码
                                useEq: this.useEq * 1, //
                                loseEq: this.selectedObj.loseEq * 1, //分摊损耗
                                allEq: this.calcAllEq, //总电量
                            }
                        } else if (this.type == 2) { //修改起止码上传日明细
                            if (!this.fileList.length) {
                                this.$message({
                                    type: 'warning',
                                    message: '请上传日明细表',
                                })
                                return;
                            }
                            if (this.selectedObj.drEq > this.calcAllEq) {
                                this.$message({
                                    type: 'warning',
                                    message: '总电量应该大于等于导入的日用表总电量',
                                })
                                return;
                            }
                            opt = {
                                id: this.selectedObj.id,
                                arEnum: this.selectedObj.arEnum * 1, //止码
                                arSnum: this.selectedObj.arSnum * 1, //止码
                                allEq: this.calcAllEq, //总电量
                                loseEq: this.selectedObj.loseEq * 1, //分摊损耗
                                useEq: this.useEq * 1, //
                                dayPaperFile: this.excelName ? this.excelName : 'excel',
                            }
                        } else if (this.type == 3) { // 上传模拟发票
                            if (!this.filesFP.length) {
                                this.$message({
                                    type: 'warning',
                                    message: '请上传模拟发票',
                                })
                                return;
                            }
                            opt = {
                                id: this.selectedObj.id,
                                allEq: this.allEqFP, //总电量
                                loseEq: this.selectedObj.loseEq * 1, //分摊损耗
                                stateGridPaper: this._getUrl(this.filesFP[0])
                            }
                        } else if (this.type == 4) {
                            if (!this.filesFP.length) {
                                this.$message({
                                    type: 'warning',
                                    message: '请上传模拟发票',
                                })
                                return;
                            }
                            opt = { //上传模拟发票+起止码
                                id: this.selectedObj.id,
                                arEnum: this.selectedObj.arEnum * 1, //止码
                                arSnum: this.selectedObj.arSnum * 1, //止码
                                useEq: this.useEq * 1, //
                                loseEq: this.selectedObj.loseEq * 1, //分摊损耗
                                allEq: this.calcAllEq, //总电量
                                stateGridPaper: this._getUrl(this.filesFP[0])

                            }
                        } else if (this.type == 6 || this.type == 7) { //市场异常
                            opt = {
                                id: this.selectedObj.id,
                                price: this.selectedObj.tempPrice,
                            }
                        }
                        this.$emit('exceptionHand', opt)
                    } else {
                        return false;
                    }
                })
            },
            inputS(val) { //起度大于等于0
                if (~val.indexOf('0.0') && Number(val) == 0) return;
                this.selectedObj.arSnum = val < 0 ? 0 : Number(val);
                //自动根据表码只计算止度 只有导入日明细的才有此逻辑
                this.selectedObj.arEnum = Number(Number(this.selectedObj.arSnum + this.calc * 1).toFixed(5));
            },
            inputE(val) {
                this.selectedObj.arEnum = Number(this.selectedObj.arEnum) < Number(this.selectedObj.arSnum) ? this
                    .selectedObj.arSnum :
                    Number(this.selectedObj.arEnum)
                this.$refs['form'].validateField('arEnum');
            },
            // inputSumber(val) {

            // },

            //日用明细表上传检查
            beforeUpload(file) {
                const name = file.name.toLowerCase();
                const isExcel = ['.xls', '.xlsx'].find(item => {
                    return name.indexOf(item) !== -1;
                })
                const isLt2M = file.size / 1024 / 1024 < 2;
                if (!isExcel) {
                    this.$message.error('请上传excel文件!');
                    return false;
                }
                if (!isLt2M) {
                    this.$message.error('上传文件大小不能超过 2MB!');
                    return false;
                }
                this.upLoading = true;
                this.excelName = name; //保存上传的excel名称
                return true;
            },
            //模拟发票格式验证
            beforeUploadFP(file) {
                if (this.$refs.uploadPreogress.isLoading()) return false;
                const name = file.name.toLowerCase();
                const isPngOrPdf = ['.png', '.jpeg', '.jpg', '.pdf'].find(item => {
                    return name.indexOf(item) !== -1;
                })
                const isLt2M = (file.size / 1024 / 1024) < 2;
                if (!isPngOrPdf) {
                    this.$message.error('请上传png、jpeg、jpg或pdf文件!');
                    return false;
                }

                this.$refs.uploadPreogress.start(); //开始增加进度

                //如果时图片且超过2M 压缩图片
                if (!isLt2M && !~name.indexOf('.pdf')) {
                    return tool.compressImg(file);
                } else {
                    return true;
                }
            },
            //获取保存的url地址
            _getUrl(item) {
                if (!item) return '';
                if (item.response) {
                    return item.response.fileName;
                } else if (item.url) {
                    return item.url.replace(baseProfileUrl, '');
                }
                return '';
            },
            //根据文件名获取文件名称
            _getFileNameByUrl(url) {
                if (!url) return '';
                const _index = url.indexOf('_');
                return _index !== -1 ? url.slice(_index + 1) : url;
            },
            // 上传回调日明细
            handleChange(res, file, fileList) {
                this.upLoading = false;
                if (res.code == 200) {
                    this.fileList = fileList;
                    this.selectedObj.drEq = res.data.useEq;
                } else {
                    this.msg(res.msg);
                    this.fileList = [];
                }
                if (this.fileList.length >= 2) {
                    this.fileList = [this.fileList[1]];
                }
            },
            // 上传回调发票
            handleChangeFP(res, file, fileList) {
                if (res.code == 200) {
                    this.filesFP = fileList.slice(0);
                    this.$refs.uploadPreogress.success();
                } else {
                    this.msg(res.msg);
                    this.filesFP = [];
                }
            },
            //获取svg文件名
            getPreType(file) {
                const name = file.name.toLowerCase();
                const baseImg = ''
                if (name.indexOf('.doc') !== -1 || name.indexOf('.docx') !== -1) { // word
                    return 'w'
                } else if (name.indexOf('.pdf') !== -1) {
                    return 'pdf'
                } else if (name.indexOf('.ppt') !== -1 || name.indexOf('.pptx') !== -1) {
                    return 'ppt'
                } else if (name.indexOf('.xls') !== -1 || name.indexOf('.xlsx') !== -1) {
                    return 's'
                }
                return 'zip';
            },
            //判断文件是否是图片
            isImg(file) {
                const name = file.name.toLowerCase();
                return ['.png', '.jpeg', '.jpg'].find(item => {
                    return name.indexOf(item) !== -1;
                })
            },
            //预览图片
            handlePictureCardPreview(file) {
                this.$viewerApi({
                    images: [file.url],
                })
            },
            //删除图片
            handleRemove(data, file) {
                let index = 0;
                data.map((item, index) => {
                    if (item.name === file.name) {
                        index = index;
                    }
                })
                data.splice(index, 1)
            },
            //获取完整绝对文件地址
            getShowUrl(url) {
                if (!url) return '';
                return baseProfileUrl + url;
            },
            goRouter() {
                this.handleClose();
                this.$router.push("/useEqManage");
            },
            error() {
                this.upLoading = false;
            },
            msg(err) {
                this.$message.error('上传出错:' + err);
            },
        },
        watch: {
            detailData: {
                handler(val) {
                    if (!val) return;
                    this.initPage(val);
                },
                deep: true,
                immediate: true,
            },
        }
    }
</script>

