<!--CusTable组件doc-->
<template>
    <div style="width: 100%" class="cus-table">
        <el-table v-table-scroll style="width: 100%" :tree-props="{children: 'subBills'}" row-key="index"
            :data="tableData" :header-cell-style="{background:'#E6EAFE',color:'#606266'}">
            <el-table-column label="序号" prop="index" width="90" fixed="left" />
            <el-table-column label="所属平台" prop="kpName" width="200" fixed="left"></el-table-column>
            <el-table-column label="用电性质" prop="workType" width="90"></el-table-column>
            <el-table-column label="账单月份" prop="arSnum" width="120">
                <template slot-scope="scope">
                    <span>{{ changeMonth(scope.row['month']) }}</span>
                </template>
            </el-table-column>
            <el-table-column label="起度" prop="arSnum" width="100">
                <template slot-scope="scope">
                    <span>{{ scope.row['type']=='3' && !scope.row['isChild'] ? '': scope.row['arSnum']}}</span>
                    <!--type为3 代表合表 更换计量点的情况 父账单没有起止度-->
                </template>
            </el-table-column>
            <el-table-column label="止度" prop="arEnum" width="100">
                <template slot-scope="scope">
                    <span>{{ scope.row['type']=='3' && !scope.row['isChild'] ? '': scope.row['arEnum']}}</span>
                    <!--type为3 代表合表 更换计量点的情况 父账单没有起止度-->
                </template>
            </el-table-column>
            <el-table-column label="倍率" prop="blRate" width="80">
                <template slot-scope="scope">
                    <span>{{ scope.row['type']=='3' && !scope.row['isChild']? '': scope.row['blRate']}}</span>
                    <!--type为3 代表合表 更换计量点的情况 父账单没有倍率-->
                </template>
            </el-table-column>
            <el-table-column label="电量（kW·h）" prop="useEq" width="120">
                <template slot-scope="scope">
                    <span>{{ scope.row['type']=='3' && !scope.row['isChild']? '': scope.row['useEq']}}</span>
                    <!--type为3 代表合表 更换计量点的情况 父账单没有倍率-->
                </template>
            </el-table-column>
            <el-table-column label="分摊损耗比例" prop="loseRate" width="120"></el-table-column>
            <el-table-column label="损耗电量" prop="loseEq" width="100"></el-table-column>
            <el-table-column label="补录电量" prop="extraEq" width="100"></el-table-column>
            <el-table-column label="总电量（kW·h）" prop="allEq" width="130"></el-table-column>
            <el-table-column label="所属公司" prop="kcName" show-overflow-tooltip width="200"></el-table-column>
            <el-table-column label="所属路线" prop="glName" show-overflow-tooltip width="200"></el-table-column>
            <el-table-column label="计量点名称" prop="jpName" show-overflow-tooltip width="250"></el-table-column>
            <el-table-column label="用能单位" prop="sdName" show-overflow-tooltip width="240"></el-table-column>
            <el-table-column label="结算起止码时间" prop="ts" width="300">
                <template slot-scope="scope">
                    <span v-if="scope.row['arSt']">
                        {{changeTime(scope.row['arSt'])}}-{{changeTime(scope.row['arEt'])}}
                    </span>
                </template>
            </el-table-column>
            <el-table-column label="日用电明细" align="left" fixed="right" class-name="cusTable" width="90">
                <template slot-scope="scope">
                    <span v-if="defined(scope.row.dayPaper)" class="cus-btn" @click="handClick('日用电明细',scope.row)"
                        type="text" size="small">查看</span>
                </template>
            </el-table-column>
            <el-table-column label="现场确认单" align="left" fixed="right" class-name="cusTable" width="90">
                <template slot-scope="scope">
                    <div class="table-btns">
                        <div class="center">
                            <span v-if="defined(scope.row.sitePaper)" class="cus-btn"
                                @click="handClick('现场电量确认单',scope.row)" type="text" size="small">查看
                            </span>
                        </div>
                    </div>
                </template>
            </el-table-column>
            <el-table-column label="操作" align="center" fixed="right" class-name="cusTable" width="220">
                <template slot-scope="scope">
                    <div class="table-btns" :class="{'three':getBtnsCount(scope.row) >= 3}">

                        <span v-if="!scope.row['isChild']" class="cus-btn line" @click="handClick('发起审核',scope.row)"
                            type="text" size="small">发起审核</span>
                        <span v-if="!scope.row['isChild']" class="cus-btn line" @click="handClick('异常处理',scope.row)"
                            type="text" size="small">异常处理</span>

                        <!--三个以上菜单按钮用下拉-->
                        <template v-if="haveMoreThreeBtns(scope.row)">
                            <el-dropdown @command="handleCommand($event,scope.row)">
                                <span class="el-dropdown-link cus-btn">
                                    编辑<i class="el-icon-arrow-down el-icon--right"></i>
                                </span>
                                <el-dropdown-menu slot="dropdown">
                                    <el-dropdown-item command="编辑名称">编辑名称</el-dropdown-item>
                                    <el-dropdown-item
                                        v-if="isYes(scope.row.stateGirdPaperImp)|| isYes(scope.row.dayPaperImp)"
                                        command="数据维护">数据维护</el-dropdown-item>
                                </el-dropdown-menu>
                            </el-dropdown>
                        </template>
                        <!--三个以下菜单按钮并排-->
                        <template v-else>
                            <span v-if="isYes(scope.row.dayPaperImp)" class="cus-btn"
                                @click="handClick('数据维护',scope.row)" type="text" size="small">数据维护
                            </span>
                            <span v-if="isYes(scope.row.wellEdit)" class="cus-btn" @click="handClick('编辑名称',scope.row)"
                                type="text" size="small">编辑名称
                            </span>
                        </template>
                    </div>
                </template>
            </el-table-column>
        </el-table>
        <div class="page pagination-box">
            <el-pagination @size-change="handleSizeChange" @current-change="handleCurrentChange" :current-page="pageNum"
                :page-sizes="[10, 20, 50, 100]" :page-size="pageSize" layout="prev, pager, next, jumper,total, sizes"
                :total="total">
            </el-pagination>
        </div>
    </div>
</template>
<script>
    import tool from "@/utils/tool";
    import Table from "@/components/ElTableCommon.vue";
    export default {
        extends: Table,
        name: 'BillCreateTable',
        mounted() {},
        data() {
            return {}
        },
        methods: {
            defined: tool.defined,
            isYes: tool.isYes,
            //获取该账单功能按钮数
            getBtnsCount(row) {
                let c = 0;
                if (!row['isChild']) c += 2;
                if (this.isYes(row.wellEdit)) c += 1;
                if (this.isYes(row.stateGirdPaperImp)) c += 1;
                if (this.isYes(row.dayPaperImp)) c += 1;
                return c;
            },
            //是否含有3个以上的功能菜单
            haveMoreThreeBtns(row) {
                return this.getBtnsCount(row) > 3;
            },
        }
    }
</script>
<style scoped lang="scss">
    .page {
        display: flex;
        justify-content: flex-end;
    }

    .cus-btn {
        color: #2E4AF7;
        font-size: 14px;
        padding-left: 25px;
        cursor: pointer;
        line-height: 22px;
        position: relative;

        &:first-child {
            padding-left: 0;
        }
    }

    .table-btns {
        display: flex;
        justify-content: left;

        &.three {
            justify-content: space-between;

            .cus-btn {
                padding: 0;
            }

            .line:after {
                content: "";
                position: absolute;
                display: block;
                right: -13px;
                top: 6px;
                bottom: 0;
                height: 12px;
                width: 1px;
                background: #E3E5EC;
            }
        }


        .center {
            width: 100%;
        }

    }


    /deep/ .active-row {
        background-color: #F5F7FA;
    }
</style>